import React, { useRef, useState, useEffect } from 'react';
import {
    Button,
    FormControl,
    FormLabel,
    TextField,
    Typography,
    Select,
    MenuItem,
    InputLabel,
    OutlinedInput,
} from '@material-ui/core';
import { useReactToPrint } from 'react-to-print';
import { useHistory, useParams } from 'react-router-dom';
import { getStudent, getConfigInfo, addItem, getItems } from '../helper/db';
import InvoiceToPrint from './InvoiceToPrint';

const root = {
    display: 'flex',
    flexDirection: 'column',
    '@media print': {
        display: 'block',
    },
};

const divButton = {
    marginLeft: '40px',
    marginRight: 'auto',
};

const button = {
    width: 'fit-content',
    marginLeft: '5px',
    marginRight: '5px',
};

const newItem = {
    display: 'flex',
    flexDirection: 'row',
};

const label = {
    marginBottom: '5px',
};

const textField = {
    width: '200px',
    marginBottom: '30px',
    marginRight: '30px',
};

const addButton = {
    width: '50px',
    height: '40px',
};

const list = {
    display: 'flex',
    flexDirection: 'row',
    marginBottom: '10px',
};

const eachItemAttribute = {
    marginRight: '20px',
};

const deleteButton = {
    width: '35px',
    height: '25px',
};

const invoiceDate = {
    width: '150px',
    marginRight: '30px',
};

const divider = {
    width: '100%',
    borderBottom: '1px solid black',
    marginTop: '30px',
    marginBottom: '30px',
};

const selectField = {
    width: '200px',
    marginBottom: '30px',
    marginRight: '30px',
};

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250,
        },
    },
};

const Invoice = () => {
    const componentRef = useRef();

    const pageStyle = `
        @page {
            size: 216mm 280mm;
        }
  `;
    const handlePrint = useReactToPrint({
        content: () => componentRef.current,
        pageStyle: pageStyle,
    });
    const [curStudent, setCurStudent] = useState({});
    const [config, setConfig] = useState({
        academyname: '',
        address: '',
        phone: '',
    });
    const history = useHistory();
    const { id } = useParams();
    const [item, setItem] = useState([]);
    const [itemName, setItemName] = useState('');
    const [price, setPrice] = useState('');
    const [date, setDate] = useState('');
    const [dueDate, setDueDate] = useState('');
    const [newAddItem, setNewAddItem] = useState('');
    const [listItems, setListItems] = useState([]);

    useEffect(() => {
        const setStudentCall = async () => {
            const result = await getStudent(id);
            if (!result) {
                history.push('/error');
                return;
            }
            setCurStudent(result);
            const resultConfig = await getConfigInfo();
            setConfig(resultConfig[0]);

            const result2 = await getItems();
            if (!result2) {
                history.push('/error');
                return;
            }
            setListItems(result2);
        };
        setStudentCall();
    }, []);

    const handleAdd = () => {
        if (itemName === '' || price === '') {
            return;
        }
        const temp = [...item];
        setItem([...temp, { name: itemName, price }]);
        setItemName('');
        setPrice('');
    };

    const handleDelete = (index) => {
        const temp = [...item];
        temp.splice(index, 1);
        setItem([...temp]);
    };

    const handleSave = async () => {
        if (newAddItem === '') {
            return;
        }
        await addItem({ itemname: newAddItem });
        const result = await getItems();
        if (!result) {
            history.push('/error');
            return;
        }
        setListItems(result);
        setNewAddItem('');
    };

    return (
        <div style={root}>
            <div style={newItem}>
                <FormControl component="fieldset">
                    <FormLabel component="legend" style={label}>
                        Item Name
                    </FormLabel>
                    <TextField
                        id="name"
                        value={newAddItem}
                        onChange={(e) => setNewAddItem(e.target.value)}
                        style={textField}
                    />
                </FormControl>
                <Button
                    onClick={() => handleSave()}
                    variant="contained"
                    color="primary"
                    style={addButton}
                >
                    SAVE
                </Button>
            </div>

            <div style={newItem}>
                <FormControl style={selectField}>
                    <InputLabel id="selectItemNameLabel">Item</InputLabel>
                    <Select
                        labelId="selectItemNameLabel"
                        id="selectItemName"
                        value={itemName}
                        onChange={(e) => setItemName(e.target.value)}
                        label="Item"
                    >
                        {listItems.map((item) => (
                            <MenuItem value={item.itemname}>
                                {item.itemname}
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
                <FormControl component="fieldset">
                    <FormLabel component="legend" style={label}>
                        Price
                    </FormLabel>
                    <TextField
                        id="price"
                        value={price}
                        onChange={(e) => setPrice(e.target.value)}
                        style={textField}
                    />
                </FormControl>
                <Button
                    onClick={() => handleAdd()}
                    variant="contained"
                    color="primary"
                    style={addButton}
                >
                    Add
                </Button>
            </div>

            {item.length > 0 && (
                <Typography variant="h6" gutterBottom style={eachItemAttribute}>
                    List of added items
                </Typography>
            )}

            {item.length > 0 &&
                item?.map((eachItem, index) => {
                    return (
                        <div style={list} key={index}>
                            <Typography
                                variant="body1"
                                gutterBottom
                                style={eachItemAttribute}
                            >
                                {eachItem.name}
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                style={eachItemAttribute}
                            >
                                {eachItem.price}
                            </Typography>
                            <Button
                                onClick={() => handleDelete(index)}
                                variant="contained"
                                color="secondary"
                                style={deleteButton}
                            >
                                DELETE
                            </Button>
                        </div>
                    );
                })}
            <div
                style={{ ...newItem, marginTop: '30px', marginBottom: '30px' }}
            >
                <FormControl component="fieldset">
                    <FormLabel component="legend" style={label}>
                        Invoice date
                    </FormLabel>
                    <TextField
                        id="invoiceDate"
                        value={date}
                        onChange={(e) => setDate(e.target.value)}
                        type="date"
                        style={invoiceDate}
                    />
                </FormControl>

                <FormControl component="fieldset">
                    <FormLabel component="legend" style={label}>
                        Due date
                    </FormLabel>
                    <TextField
                        id="dueDate"
                        value={dueDate}
                        onChange={(e) => setDueDate(e.target.value)}
                        type="date"
                        style={invoiceDate}
                    />
                </FormControl>
            </div>
            <div style={divider}></div>
            <InvoiceToPrint
                ref={componentRef}
                curStudent={curStudent}
                config={config}
                items={item}
                invoiceDate={date}
                dueDate={dueDate}
            />
            <div style={divButton}>
                <Button
                    onClick={() => history.push(`/detailstudent/${id}`)}
                    variant="contained"
                    style={button}
                >
                    Back
                </Button>
                <Button
                    onClick={handlePrint}
                    variant="contained"
                    color="primary"
                    style={button}
                >
                    PRINT
                </Button>
            </div>
        </div>
    );
};

export default Invoice;
