import React, { useState } from 'react';
import {
    Avatar,
    Button,
    CssBaseline,
    TextField,
    Link,
    Box,
    Typography,
    Snackbar,
} from '@material-ui/core';
import MuiAlert from '@material-ui/lab/Alert';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import axios from 'axios';

import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { useHistory } from 'react-router-dom';
import { useCookies } from 'react-cookie';

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright  '}
            <Link color="inherit" href="https://ryanwlee.github.io/">
                Academy Helper
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%',
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

export default function Login() {
    const classes = useStyles();
    const [userName, setUserName] = useState('');
    const [password, setPassword] = useState('');
    const [server, setServer] = useState('');
    const [open, setOpen] = React.useState(false);
    const [errorUserName, setErrorUserName] = useState('');
    const [errorPassword, setErrorPassword] = useState('');
    const [errorServer, setErrorServer] = useState('');
    const [cookies, setCookie] = useCookies(['auth']);
    let history = useHistory();

    const handleUserName = (e) => {
        setUserName(e.target.value);
    };
    const handlePassword = (e) => {
        setPassword(e.target.value);
    };
    const handleServer = (e) => {
        setServer(e.target.value);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (
            errorUserName.length > 0 ||
            errorPassword.length > 0 ||
            errorServer.length > 0
        ) {
            setOpen(true);
        }

        try {
            const response = await axios.post(
                `${server}/signin`,
                JSON.stringify({
                    userName,
                    password,
                }),
                {
                    headers: { 'Content-Type': 'application/json' },
                    mode: 'no-cors',
                }
            );

            if (
                response &&
                response.status === 200 &&
                response.data === 'success'
            ) {
                setCookie('auth', true, { path: '/', maxAge: 1200 });
                localStorage.setItem(
                    'serverUrl',
                    JSON.stringify({
                        server,
                    })
                );
                history.push('/home');
            } else {
                setOpen(true);
            }
        } catch (err) {
            setOpen(true);
        }
    };

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };

    const handleVerifyUserName = () => {
        if (userName.includes(' ')) {
            setErrorUserName(
                'Username contains space. Please put valid username.'
            );
        } else if (userName.length === 0) {
            setErrorUserName('Username is required.');
        } else {
            setErrorUserName('');
        }
        return;
    };

    const handleVerifyPassword = () => {
        if (password.includes(' ')) {
            setErrorPassword(
                'Password contains space. Please put valid password.'
            );
        } else if (password.length === 0) {
            setErrorPassword('Password is required.');
        } else {
            setErrorPassword('');
        }
        return;
    };

    const handleVerifyServer = () => {
        if (server.includes(' ')) {
            setErrorServer(
                'Server address contains space. Please put valid server address.'
            );
        } else if (server.length === 0) {
            setErrorServer('Server address is required.');
        } else {
            setErrorServer('');
        }
        return;
    };

    return (
        <Container component="main" maxWidth="xs">
            <Snackbar
                open={open}
                autoHideDuration={6000}
                onClose={handleClose}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
                <Alert onClose={handleClose} severity="error">
                    Username, password, or server address is wrong. Please try
                    again!
                </Alert>
            </Snackbar>
            <CssBaseline />
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Sign in
                </Typography>
                <form className={classes.form} noValidate>
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="username"
                        label="Username"
                        name="username"
                        autoComplete="username"
                        onChange={handleUserName}
                        onBlur={handleVerifyUserName}
                        error={errorUserName.length !== 0}
                        helperText={errorUserName}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        name="password"
                        label="Password"
                        type="password"
                        id="password"
                        autoComplete="current-password"
                        onChange={handlePassword}
                        onBlur={handleVerifyPassword}
                        error={errorPassword.length !== 0}
                        helperText={errorPassword}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="server"
                        label="Server"
                        name="server"
                        autoComplete="server"
                        onChange={handleServer}
                        onBlur={handleVerifyServer}
                        error={errorServer.length !== 0}
                        helperText={errorServer}
                    />
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                        onClick={handleSubmit}
                    >
                        Sign In
                    </Button>
                </form>
            </div>
            <Box mt={8}>
                <Copyright />
            </Box>
        </Container>
    );
}
