export const attendStudents = async (students, date) => {
    const nowDate = moment(date).format('YYYYMMDD');
    try {
        const response = await axios.post(
            `${host}/attendStudents`,
            JSON.stringify({ students, nowDate }),
            {
                headers: { 'Content-Type': 'application/json' },
                mode: 'no-cors',
            }
        );
        if (
            response &&
            response.status === 200 &&
            response.data === 'success'
        ) {
            return 'success';
        } else {
            return 'fail';
        }
    } catch (e) {
        if (e.response.data.code === '23505') {
            return 'fail2';
        }
        return 'fail';
    }
};

