import React from 'react';
import { Button, Typography } from '@material-ui/core';
import { useHistory, useParams } from 'react-router-dom';
import Webcam from 'react-webcam';
import { useRecoilState } from 'recoil';
import SaveModal from './SaveModal';
import { SnackBarState } from './recoilHelper';
import { savePicture } from '../helper/db';

const root = {
    display: 'flex',
    flexDirection: 'column',
};

const button = {
    width: 'fit-content',
    margin: '0 auto 30px auto',
};

const videoConstraints = {
    width: 180,
    height: 220,
    facingMode: 'user',
};

const webcamContainer = {
    display: 'flex',
    flexDirection: 'row',
    margin: '0 auto 30px auto',
    gap: '20px',
};

const imageContainer = {
    display: 'flex',
    flexDirection: 'column',
    gap: '10px',
};

const buttonContainer = {
    display: 'flex',
    flexDirection: 'row',
    margin: '0 auto 30px auto',
    gap: '20px',
};

const capturedImage = {
    width: videoConstraints.width,
    height: videoConstraints.height,
};

const TakePicture = () => {
    const webcamRef = React.useRef(null);
    const [picture, setPicture] = React.useState('');
    const [saveConfirmOpen, setSaveConfirmOpen] = React.useState(false);
    const [snackBarState, setSnackBarState] = useRecoilState(SnackBarState);
    const { id: studentid } = useParams();
    const history = useHistory();

    const handleSaveConfirmOpen = () => {
        setSaveConfirmOpen(true);
    };

    const handleSaveConfirmClose = () => {
        setSaveConfirmOpen(false);
    };

    const savePictureHandler = () => async () => {
        const response = await savePicture({
            studentid,
            picture,
        });
        if (response === 'success') {
            setSnackBarState({ ...snackBarState, openSuccess: true });
            setPicture('');
        } else {
            setSnackBarState({ ...snackBarState, openError: true });
        }
    };

    const capture = React.useCallback(() => {
        const imageSrc = webcamRef.current.getScreenshot();
        setPicture(imageSrc);
    }, [webcamRef]);

    return (
        <div style={root}>
            <SaveModal
                open={saveConfirmOpen}
                onClose={handleSaveConfirmClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                saveHandler={savePictureHandler()}
            />
            <div style={webcamContainer}>
                <div style={imageContainer}>
                    <Typography variant="h6" gutterBottom>
                        Camera
                    </Typography>
                    <Webcam
                        audio={false}
                        height={videoConstraints.height}
                        ref={webcamRef}
                        screenshotFormat="image/jpeg"
                        width={videoConstraints.width}
                        videoConstraints={videoConstraints}
                    />
                </div>
                {picture === '' ? (
                    <></>
                ) : (
                    <div style={imageContainer}>
                        <Typography variant="h6" gutterBottom>
                            Captured
                        </Typography>
                        <img src={picture} style={capturedImage} />{' '}
                    </div>
                )}
            </div>
            <div style={buttonContainer}>
                <Button
                    onClick={() => history.push(`/detailstudent/${studentid}`)}
                    variant="contained"
                    style={button}
                >
                    Back
                </Button>
                <Button
                    onClick={capture}
                    variant="contained"
                    color="secondary"
                    style={button}
                >
                    Capture
                </Button>
                {picture === '' ? (
                    <></>
                ) : (
                    <Button
                        onClick={() => handleSaveConfirmOpen()}
                        variant="contained"
                        color="primary"
                        style={button}
                    >
                        Save
                    </Button>
                )}
            </div>
        </div>
    );
};

export default TakePicture;
