import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
    Typography,
    TextField,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Divider,
    Button,
    Snackbar,
    InputAdornment,
    FormControlLabel,
    Checkbox,
} from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import { addNewStudent, getConfigInfo } from '../../helper/db';
import MuiAlert from '@material-ui/lab/Alert';
import FormHelperText from '@material-ui/core/FormHelperText';
import { initState, beltOptions } from '../../helper/constant';
import SaveModal from '../SaveModal';
import MuiPhoneNumber from 'material-ui-phone-number';
import { SnackBarState } from '../recoilHelper';
import { useRecoilState } from 'recoil';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
    },
    form: {
        display: 'flex',
        flexDirection: 'column',
    },
    line: {
        display: 'flex',
        flexDirection: 'row',
    },
    field: {
        marginRight: '2em',
    },
    formControl: {
        margin: theme.spacing(2),
        minWidth: 240,
        maxWidth: 240,
        flexGrow: 1,
    },
    formControlHalf: {
        margin: theme.spacing(2),
        minWidth: 120,
        maxWidth: 120,
        flexGrow: 1,
    },
    formControlLong: {
        margin: theme.spacing(2),
        minWidth: 120,
        maxWidth: 700,
        flexGrow: 1,
    },
    formControlLabel: {
        margin: theme.spacing(2),
    },
    buttonContainer: { display: 'flex', marginTop: theme.spacing(4) },
    button: {
        marginRight: theme.spacing(2),
        width: '200px',
    },
}));

export default function AddStudent() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [openError, setOpenError] = React.useState(false);
    const [openErrorField, setOpenErrorField] = React.useState(false);
    const [saveConfirmOpen, setSaveConfirmOpen] = React.useState(false);
    const [snackBarState, setSnackBarState] = useRecoilState(SnackBarState);
    const [beltSetting, setBeltSetting] = React.useState('a');

    useEffect(() => {
        const setConfig = async () => {
            const result = await getConfigInfo();
            if (result === 'fail') {
                setSnackBarState({ ...snackBarState, openError: true });
            }
            setBeltSetting(result[0].beltsetting);
        };
        setConfig();
    }, []);

    const handleSaveConfirmOpen = () => {
        setSaveConfirmOpen(true);
    };

    const handleSaveConfirmClose = () => {
        setSaveConfirmOpen(false);
    };

    // student states
    const [student, setStudent] = React.useState({
        ...initState,
    });

    const [error, setError] = React.useState({
        firstname: false,
        lastname: false,
        sex: false,
        dateofbirth: false,
        age: false,
        lessonclass: false,
        belt: false,
        studentyear: false,
    });

    const history = useHistory();

    const validate = () => {
        const tempError = { ...error };
        tempError.firstname = student.firstname.trim() === '' ? true : false;
        tempError.lastname = student.lastname.trim() === '' ? true : false;
        tempError.sex = student.sex === '' ? true : false;
        tempError.dateofbirth = student.dateofbirth === '' ? true : false;
        tempError.lessonclass = student.lessonclass === '' ? true : false;
        tempError.belt = student.belt === '' ? true : false;
        tempError.studentyear = student.studentyear === '' ? true : false;

        const isError = Object.values(tempError).includes(true);
        if (isError) {
            setOpenErrorField(true);
        }
        setError(tempError);
        return isError;
    };

    const validateEach = (field, value) => {
        const tempError = { ...error };
        tempError[field] = value.trim() === '' ? true : false;

        setError(tempError);
    };

    const initialize = () => {
        setStudent({ ...initState });
    };

    const addNewStudentHandler = () => async () => {
        const validationResult = validate();
        if (validationResult) {
            return;
        }

        const response = await addNewStudent({
            ...student,
            active: true,
        });
        if (response === 'success') {
            initialize();
            setOpen(true);
        } else {
            setOpenError(true);
        }
    };

    const handleCloseErrorField = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpenErrorField(false);
    };

    const handleCloseError = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpenError(false);
    };

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };

    const handleChange = (field, value) => {
        setStudent({ ...student, [field]: value });
    };

    // TODO: Max number of characters of each fields?

    return (
        <div className={classes.root}>
            <Snackbar
                open={open}
                autoHideDuration={6000}
                onClose={handleClose}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
                <Alert onClose={handleClose} severity="info">
                    Successfully saved!
                </Alert>
            </Snackbar>
            <Snackbar
                open={openError}
                autoHideDuration={6000}
                onClose={handleCloseError}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
                <Alert onClose={handleCloseError} severity="error">
                    Something went wrong. Please try again.
                </Alert>
            </Snackbar>
            <Snackbar
                open={openErrorField}
                autoHideDuration={6000}
                onClose={handleCloseErrorField}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
                <Alert onClose={handleCloseErrorField} severity="error">
                    Some required fields are not valid
                </Alert>
            </Snackbar>

            <SaveModal
                open={saveConfirmOpen}
                onClose={handleSaveConfirmClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                saveHandler={addNewStudentHandler()}
            />

            <form className={classes.form}>
                <div className={classes.line}>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="firstname"
                            label="First Name"
                            value={student.firstname}
                            onChange={(e) =>
                                handleChange('firstname', e.target.value)
                            }
                            onBlur={() =>
                                validateEach('firstname', student.firstname)
                            }
                            error={error.firstname}
                            helperText={
                                error.firstname
                                    ? 'Please put valid first name'
                                    : ''
                            }
                        />
                    </FormControl>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="lastname"
                            label="Last Name"
                            value={student.lastname}
                            onChange={(e) =>
                                handleChange('lastname', e.target.value)
                            }
                            onBlur={() =>
                                validateEach('lastname', student.lastname)
                            }
                            error={error.lastname}
                            helperText={
                                error.lastname
                                    ? 'Please put valid last name'
                                    : ''
                            }
                        />
                    </FormControl>
                    <FormControl
                        className={classes.formControlHalf}
                        error={error.sex}
                    >
                        <InputLabel id="sexLabel">Sex</InputLabel>
                        <Select
                            labelId="sexSelect"
                            id="sexSelect"
                            value={student.sex}
                            onChange={(e) =>
                                handleChange('sex', e.target.value)
                            }
                            onBlur={() => validateEach('sex', student.sex)}
                        >
                            <MenuItem value={'male'}>Male</MenuItem>
                            <MenuItem value={'female'}>Female</MenuItem>
                        </Select>
                        <FormHelperText>
                            {error.sex ? 'Please select sex' : ''}
                        </FormHelperText>
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="dob"
                            label="Date of Birth"
                            type="date"
                            InputLabelProps={{
                                shrink: true,
                            }}
                            onChange={(e) =>
                                handleChange('dateofbirth', e.target.value)
                            }
                            value={student.dateofbirth}
                            error={error.dateofbirth}
                            helperText={
                                error.dateofbirth
                                    ? 'Please put date of birth'
                                    : ''
                            }
                            onBlur={() =>
                                validateEach('dateofbirth', student.dateofbirth)
                            }
                        />
                    </FormControl>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="email"
                            label="Email"
                            value={student.email}
                            onChange={(e) =>
                                handleChange('email', e.target.value)
                            }
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControl}>
                        <MuiPhoneNumber
                            id="phonecell"
                            label="Phone(Cell)"
                            value={student.phonecell}
                            onChange={(value) =>
                                handleChange('phonecell', value)
                            }
                            defaultCountry="ca"
                            disableAreaCodes={true}
                            countryCodeEditable={true}
                            disableDropdown={true}
                        />
                    </FormControl>
                    <FormControl className={classes.formControl}>
                        <MuiPhoneNumber
                            id="phonehome"
                            label="Phone(Home)"
                            value={student.phonehome}
                            onChange={(value) =>
                                handleChange('phonehome', value)
                            }
                            defaultCountry="ca"
                            disableAreaCodes={true}
                            countryCodeEditable={true}
                            disableDropdown={true}
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl
                        className={classes.formControlHalf}
                        error={error.lessonclass}
                    >
                        <InputLabel id="classLabel">Class</InputLabel>
                        <Select
                            labelId="classSelect"
                            id="classSelect"
                            value={student.lessonclass}
                            onChange={(e) =>
                                handleChange('lessonclass', e.target.value)
                            }
                            onBlur={() =>
                                validateEach('lessonclass', student.lessonclass)
                            }
                        >
                            <MenuItem value={'little'}>Little</MenuItem>
                            <MenuItem value={'kid'}>Kid</MenuItem>
                            <MenuItem value={'adult'}>Adult</MenuItem>
                        </Select>
                        <FormHelperText>
                            {error.lessonclass ? 'Please select class' : ''}
                        </FormHelperText>
                    </FormControl>
                    <FormControl
                        className={classes.formControlHalf}
                        error={error.studentyear}
                    >
                        <InputLabel id="yearLabel">Year</InputLabel>
                        <Select
                            labelId="yearSelect"
                            id="yearSelect"
                            value={student.studentyear}
                            onChange={(e) =>
                                handleChange('studentyear', e.target.value)
                            }
                            onBlur={() =>
                                validateEach('studentyear', student.studentyear)
                            }
                        >
                            <MenuItem value={'osc'}>O.S.C</MenuItem>
                            <MenuItem value={'m2m'}>M2M</MenuItem>
                            <MenuItem value={'1yr'}>1YR</MenuItem>
                            <MenuItem value={'2yr'}>2YR</MenuItem>
                            <MenuItem value={'3yr'}>3YR</MenuItem>
                        </Select>
                        <FormHelperText>
                            {error.studentyear ? 'Please select year' : ''}
                        </FormHelperText>
                    </FormControl>
                </div>
                <div className={classes.line}>
                    <FormControl
                        className={classes.formControl}
                        error={error.belt}
                    >
                        <InputLabel id="beltLabel">Belt</InputLabel>
                        {beltOptions(
                            (e) => handleChange('belt', e.target.value),
                            () => validateEach('belt', student.belt),
                            student.belt,
                            beltSetting
                        )}
                        <FormHelperText>
                            {error.belt ? 'Please select belt' : ''}
                        </FormHelperText>
                    </FormControl>
                    <FormControl className={classes.formControlHalf}>
                        <InputLabel id="beltsize">Belt Size</InputLabel>
                        <Select
                            labelId="yearSelect"
                            id="yearSelect"
                            value={student.beltsize}
                            onChange={(e) =>
                                handleChange('beltsize', e.target.value)
                            }
                        >
                            <MenuItem value={'0(200)'}>0(200)</MenuItem>
                            <MenuItem value={'1(220)'}>1(220)</MenuItem>
                            <MenuItem value={'2(240)'}>2(240)</MenuItem>
                            <MenuItem value={'3(260)'}>3(260)</MenuItem>
                            <MenuItem value={'4(280)'}>4(280)</MenuItem>
                            <MenuItem value={'5(300)'}>5(300)</MenuItem>
                            <MenuItem value={'6(320)'}>6(320)</MenuItem>
                        </Select>
                    </FormControl>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="lastbelttest"
                            label="Last Belt Test"
                            type="date"
                            InputLabelProps={{
                                shrink: true,
                            }}
                            onChange={(e) =>
                                handleChange('lastbelttest', e.target.value)
                            }
                            value={student.lastbelttest}
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="address"
                            label="Address"
                            value={student.studentaddress}
                            onChange={(e) =>
                                handleChange('studentaddress', e.target.value)
                            }
                        />
                    </FormControl>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="city"
                            label="City"
                            value={student.city}
                            onChange={(e) =>
                                handleChange('city', e.target.value)
                            }
                        />
                    </FormControl>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="province"
                            label="Province"
                            value={student.province}
                            onChange={(e) =>
                                handleChange('province', e.target.value)
                            }
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="postalcode"
                            label="Postal Code"
                            value={student.postalcode}
                            onChange={(e) =>
                                handleChange('postalcode', e.target.value)
                            }
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="howdidyouhear"
                            label="How did you hear about us"
                            value={student.howdidyouhear}
                            onChange={(e) =>
                                handleChange('howdidyouhear', e.target.value)
                            }
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="school"
                            label="School Attending"
                            value={student.school}
                            onChange={(e) =>
                                handleChange('school', e.target.value)
                            }
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="medicalhistory"
                            label="Medical History/Concerns"
                            value={student.medicalhistory}
                            onChange={(e) =>
                                handleChange('medicalhistory', e.target.value)
                            }
                        />
                    </FormControl>
                </div>

                <Divider
                    variant="middle"
                    style={{ marginTop: '2em', marginBottom: '2em' }}
                />

                <Typography variant="h6" gutterBottom>
                    Office Use Only
                </Typography>

                <div className={classes.line}>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="startdate"
                            label="Start Date"
                            type="date"
                            InputLabelProps={{
                                shrink: true,
                            }}
                            onChange={(e) =>
                                handleChange('startdate', e.target.value)
                            }
                            value={student.startdate}
                        />
                    </FormControl>
                    <FormControl className={classes.formControl}>
                        <TextField
                            id="enddate"
                            label="End Date"
                            type="date"
                            InputLabelProps={{
                                shrink: true,
                            }}
                            onChange={(e) =>
                                handleChange('enddate', e.target.value)
                            }
                            value={student.enddate}
                        />
                    </FormControl>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="tuition"
                            label="Tuition"
                            value={student.tuition}
                            onChange={(e) =>
                                handleChange('tuition', e.target.value)
                            }
                            InputProps={{
                                startAdornment: (
                                    <InputAdornment position="start">
                                        $
                                    </InputAdornment>
                                ),
                            }}
                            style={{ width: '150px' }}
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={student?.carestudent}
                                onChange={(e) =>
                                    handleChange(
                                        'carestudent',
                                        e.target.checked
                                    )
                                }
                                name="carestudent"
                                color="primary"
                            />
                        }
                        label="Care Student"
                    />
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={student?.preauth}
                                onChange={(e) =>
                                    handleChange('preauth', e.target.checked)
                                }
                                name="preauth"
                                color="primary"
                            />
                        }
                        label="Pre-Authorized Debit"
                    />
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={student?.etran}
                                onChange={(e) =>
                                    handleChange('etran', e.target.checked)
                                }
                                name="etran"
                                color="primary"
                            />
                        }
                        label="E-Transfer"
                    />
                    <FormControlLabel
                        control={
                            <Checkbox
                                checked={student?.creditcard}
                                onChange={(e) =>
                                    handleChange('creditcard', e.target.checked)
                                }
                                name="creditcard"
                                color="primary"
                            />
                        }
                        label="Credit Card"
                    />
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="note"
                            label="Note"
                            value={student.note}
                            onChange={(e) =>
                                handleChange('note', e.target.value)
                            }
                            multiline
                        />
                    </FormControl>
                </div>

                <Divider
                    variant="middle"
                    style={{ marginTop: '2em', marginBottom: '2em' }}
                />

                <Typography variant="h6" gutterBottom>
                    Parent/Guardian Information
                </Typography>

                <div className={classes.line}>
                    <FormControl className={classes.formControlLabel}>
                        <Typography variant="body1" gutterBottom>
                            1.
                        </Typography>
                    </FormControl>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="parent1firstname"
                            label="First Name"
                            value={student.parent1firstname}
                            onChange={(e) =>
                                handleChange('parent1firstname', e.target.value)
                            }
                        />
                    </FormControl>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="parent1lastname"
                            label="Last Name"
                            value={student.parent1lastname}
                            onChange={(e) =>
                                handleChange('parent1lastname', e.target.value)
                            }
                        />
                    </FormControl>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="parent1occupation"
                            label="Occupation"
                            value={student.parent1occupation}
                            onChange={(e) =>
                                handleChange(
                                    'parent1occupation',
                                    e.target.value
                                )
                            }
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="parent1email"
                            label="Email"
                            value={student.parent1email}
                            onChange={(e) =>
                                handleChange('parent1email', e.target.value)
                            }
                        />
                    </FormControl>
                    <FormControl className={classes.formControlLong}>
                        <MuiPhoneNumber
                            id="parent1phonecell"
                            label="Phone(Cell)"
                            value={student.parent1phonecell}
                            onChange={(value) =>
                                handleChange('parent1phonecell', value)
                            }
                            defaultCountry="ca"
                            disableAreaCodes={true}
                            countryCodeEditable={true}
                            disableDropdown={true}
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControlLabel}>
                        <Typography variant="body1" gutterBottom>
                            2.
                        </Typography>
                    </FormControl>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="parent2firstname"
                            label="First Name"
                            value={student.parent2firstname}
                            onChange={(e) =>
                                handleChange('parent2firstname', e.target.value)
                            }
                        />
                    </FormControl>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="parent2lastname"
                            label="Last Name"
                            value={student.parent2lastname}
                            onChange={(e) =>
                                handleChange('parent2lastname', e.target.value)
                            }
                        />
                    </FormControl>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="parent2occupation"
                            label="Occupation"
                            value={student.parent2occupation}
                            onChange={(e) =>
                                handleChange(
                                    'parent2occupation',
                                    e.target.value
                                )
                            }
                        />
                    </FormControl>
                </div>

                <div className={classes.line}>
                    <FormControl className={classes.formControlLong}>
                        <TextField
                            id="parent2email"
                            label="Email"
                            value={student.parent2email}
                            onChange={(e) =>
                                handleChange('parent2email', e.target.value)
                            }
                        />
                    </FormControl>
                    <FormControl className={classes.formControlLong}>
                        <MuiPhoneNumber
                            id="parent2phonecell"
                            label="Phone(Cell)"
                            value={student.parent2phonecell}
                            onChange={(value) =>
                                handleChange('parent2phonecell', value)
                            }
                            defaultCountry="ca"
                            disableAreaCodes={true}
                            countryCodeEditable={true}
                            disableDropdown={true}
                        />
                    </FormControl>
                </div>
            </form>
            <div className={classes.buttonContainer}>
                <Button
                    variant="contained"
                    className={classes.button}
                    onClick={() => history.push('/home')}
                >
                    Cancel
                </Button>
                <Button
                    variant="contained"
                    color="primary"
                    className={classes.button}
                    onClick={() => handleSaveConfirmOpen()}
                >
                    Add
                </Button>
            </div>
        </div>
    );
}
