import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Button, Chip, Snackbar } from '@material-ui/core';
import {
    getStudent,
    activeStudent,
    deleteStudent,
    getStudentAttendance,
    getPicture,
    getConfigInfo,
} from '../helper/db';
import { useHistory, useParams } from 'react-router-dom';
import MuiAlert from '@material-ui/lab/Alert';
import { useRecoilState } from 'recoil';
import { CurStudentState, IdState, SnackBarState } from './recoilHelper';
import moment from 'moment';
import DeleteModal from './DeleteModal';
import { beltMapper } from '../helper/constant';
import { calculateAge } from '../helper/util';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
    root: {
        margin: '30px auto',
    },
    textField: {
        marginLeft: theme.spacing(1),
        marginRight: theme.spacing(1),
        width: '25ch',
    },
    page: {
        flexDirection: 'row',
        backgroundColor: '#E4E4E4',
    },
    section: {
        margin: 10,
        padding: 10,
        flexGrow: 1,
    },
    // Rendering detail student
    firstSection: {
        display: 'flex',
        flexDirection: 'row',
    },
    firstSectionLeft: {
        border: '1px solid #9e9e9e',
        width: '200px',
        height: '300px',
        marginRight: '20px',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-between',
    },
    firstSectionRight: {
        display: 'flex',
        flexDirection: 'column',
    },
    row: {
        display: 'flex',
        flexDirection: 'row',
        margin: '10px 10px',
    },
    firstSectionField: {
        marginRight: '20px',
        display: 'flex',
        flexDirection: 'row',
    },
    secondSection: {
        display: 'flex',
        flexDirection: 'column',
        borderBottom: '1px solid #9e9e9e',
        paddingBottom: '20px',
    },
    secondSectionField: {
        marginTop: '20px',
        display: 'flex',
        flexDirection: 'row',
    },
    thirdSection: {
        display: 'flex',
        flexDirection: 'column',
        marginTop: '20px',
        paddingBottom: '20px',
        borderBottom: '1px solid #9e9e9e',
    },
    thirdBox: {
        display: 'flex',
        flexDirection: 'row',
    },
    emptyLeft: {
        width: '30px',
    },
    thirdSectionField: {
        marginRight: '20px',
        display: 'flex',
        flexDirection: 'row',
    },
    thirdRow: {
        display: 'flex',
        flexDirection: 'row',
        marginBottom: '20px',
    },
    flexColumn: {
        display: 'flex',
        flexDirection: 'column',
    },
    fourthSection: {
        display: 'flex',
        flexDirection: 'column',
        marginTop: '20px',
        borderBottom: '1px solid #9e9e9e',
    },
    fourthRow: {
        display: 'flex',
        flexDirection: 'row',
        marginBottom: '20px',
    },
    fourthSectionField: {
        marginRight: '20px',
        display: 'flex',
        flexDirection: 'row',
    },
    subHeader: {
        marginBottom: '20px',
    },
    noteSection: {
        paddingTop: '20px',
        paddingBottom: '30px',
        borderBottom: '1px solid #9e9e9e',
    },
    note: {
        height: '300px',
        overflowY: 'scroll',
    },
    button: {
        marginTop: '30px',
        marginRight: '20px',
    },
    rowChip: {
        textAlign: 'center',
        marginTop: '5px',
        marginBottom: '5px',
    },
    chip: {
        width: '100px',
    },
    underline: {
        textDecoration: 'underline',
        marginLeft: '5px',
        minWidth: '50px',
    },
    capturedImage: {
        width: '180px',
        height: '220px',
        margin: 'auto',
    },
    captureButton: {
        marginTop: '5px',
        marginLeft: 'auto',
        marginRight: 'auto',
        width: '100px',
    },
}));

export default function DetailStudent() {
    const classes = useStyles();
    const [student, setStudent] = useState([]);
    const [loading, setLoading] = useState(true);
    const [open, setOpen] = React.useState(false);
    const [openError, setOpenError] = React.useState(false);
    const [attendance, setAttendance] = useState([]);
    const [picture, setPicture] = useState('');

    const [curStudent, setCurStudent] = useRecoilState(CurStudentState);
    const [deleteConfirmOpen, setDeleteConfirmOpen] = React.useState(false);
    const [snackBarState, setSnackBarState] = useRecoilState(SnackBarState);

    // belt
    const [beltSetting, setBeltSetting] = React.useState('a');

    const history = useHistory();
    const { id } = useParams();

    const handleDeleteConfirmOpen = () => {
        setDeleteConfirmOpen(true);
    };

    const handleDeleteConfirmClose = () => {
        setDeleteConfirmOpen(false);
    };

    const handleActive = async (id, active) => {
        const result = await activeStudent(id, !active);
        if (result) {
            const result = await getStudent(id);
            setStudent(result);
            setOpen(true);
        } else {
            setOpenError(true);
        }
    };

    const handleDelete = () => async () => {
        const result = await deleteStudent(id);
        if (result) {
            setSnackBarState({ ...snackBarState, openSuccess: true });
            history.push('/activestudents');
        } else {
            setSnackBarState({ ...snackBarState, openError: true });
        }
    };

    const handleCopy = () => {
        setCurStudent({
            ...student,
            firstname: '',
            lastname: '',
            sex: '',
            dateofbirth: '',
            age: '',
            medicalhistory: '',
            isDone: true,
        });
        history.push('/copystudent');
    };

    const handleEdit = () => {
        history.push(`/editstudent/${id}`);
    };

    const handlePrintIdCard = () => {
        setCurStudent({ ...student, isDone: true });
        history.push(`/printidcard/${id}`);
    };

    const handlePrintPADForm = () => {
        setCurStudent({ ...student, isDone: true });
        history.push(`/printpadform/${id}`);
    };

    const handlePrintInvoice = () => {
        setCurStudent({ ...student, isDone: true });
        history.push(`/printinvoice/${id}`);
    };

    const handleCloseError = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpenError(false);
    };

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };

    const handleCapture = () => {
        history.push(`/takepicture/${id}`);
    };

    useEffect(() => {
        const setStudentCall = async () => {
            const resultBelt = await getConfigInfo();
            if (resultBelt === 'fail') {
                setSnackBarState({ ...snackBarState, openError: true });
            }
            setBeltSetting(resultBelt[0].beltsetting);

            const result = await getStudent(id);
            if (!result) {
                history.push('/error');
                return;
            }
            setStudent(result);
            const resultAttendance = await getStudentAttendance(id);
            if (!resultAttendance) {
                history.push('/error');
                return;
            }
            setAttendance(resultAttendance);
            const resultPicture = await getPicture(id);
            if (!resultPicture) {
                history.push('/error');
                return;
            }
            if (resultPicture?.length > 0) {
                setPicture(resultPicture[0].picture);
            }
            setLoading(false);
        };
        setStudentCall();
    }, []);

    const displayAttendance = () => {
        const sortedAttendance = attendance.sort(function (a, b) {
            return b.lastdate - a.lastdate;
        });
        const result = sortedAttendance.map((obj) => {
            return (
                <li>{moment(obj.lastdate, 'YYYYMMDD').format('YYYY-MM-DD')}</li>
            );
        });
        return <ul>{result}</ul>;
    };

    return (
        <div className={classes.root}>
            <Snackbar
                open={open}
                autoHideDuration={6000}
                onClose={handleClose}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
                <Alert onClose={handleClose} severity="info">
                    Successfully saved!
                </Alert>
            </Snackbar>
            <Snackbar
                open={openError}
                autoHideDuration={6000}
                onClose={handleCloseError}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
                <Alert onClose={handleCloseError} severity="error">
                    Something went wrong. Please try again.
                </Alert>
            </Snackbar>

            <DeleteModal
                open={deleteConfirmOpen}
                onClose={handleDeleteConfirmClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
                deleteHandler={handleDelete()}
            />

            <>
                <div className={classes.firstSection}>
                    <div className={classes.firstSectionLeft}>
                        {picture !== '' && (
                            <img
                                src={picture}
                                className={classes.capturedImage}
                            />
                        )}
                        <Button
                            onClick={handleCapture}
                            variant="contained"
                            color="secondary"
                            className={classes.captureButton}
                        >
                            Capture
                        </Button>
                        <div className={classes.rowChip}>
                            <Chip
                                label={student.active ? 'Active' : 'Inactive'}
                                color={student.active ? 'primary' : 'secondary'}
                                className={classes.chip}
                            />
                        </div>
                    </div>
                    <div className={classes.firstSectionRight}>
                        <div className={classes.row}>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                First name:
                                <div className={classes.underline}>
                                    {student.firstname}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Last name:
                                <div className={classes.underline}>
                                    {student.lastname}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Sex:
                                <div className={classes.underline}>
                                    {student.sex}
                                </div>
                            </Typography>
                        </div>
                        <div className={classes.row}>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                D.O.B:
                                <div className={classes.underline}>
                                    {student.dateofbirth === 'undefined'
                                        ? ''
                                        : student.dateofbirth}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Age:
                                <div className={classes.underline}>
                                    {calculateAge(student.dateofbirth)}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Email:
                                <div className={classes.underline}>
                                    {student.email}
                                </div>
                            </Typography>
                        </div>
                        <div className={classes.row}>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Phone(cell):
                                <div className={classes.underline}>
                                    {student.phonecell}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Phone(home):
                                <div className={classes.underline}>
                                    {student.phonehome}
                                </div>
                            </Typography>
                        </div>
                        <div className={classes.row}>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Class:
                                <div className={classes.underline}>
                                    {student.lessonclass === 'undefined'
                                        ? ''
                                        : student.lessonclass}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Belt:
                                <div className={classes.underline}>
                                    {student.belt === 'undefined'
                                        ? ''
                                        : beltMapper(student.belt, beltSetting)}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Year:
                                <div className={classes.underline}>
                                    {student.studentyear === 'undefined'
                                        ? ''
                                        : student.studentyear}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Last Belt Test:
                                <div className={classes.underline}>
                                    {student.lastbelttest === 'undefined'
                                        ? ''
                                        : student.lastbelttest}
                                </div>
                            </Typography>
                        </div>
                        <div className={classes.row}>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Address:
                                <div className={classes.underline}>
                                    {student.studentaddress === 'undefined'
                                        ? ''
                                        : student.studentaddress}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                City:
                                <div className={classes.underline}>
                                    {student.city}
                                </div>
                            </Typography>
                        </div>
                        <div className={classes.row}>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Province:
                                <div className={classes.underline}>
                                    {student.province}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Postal code:
                                <div className={classes.underline}>
                                    {student.postalcode}
                                </div>
                            </Typography>
                            <Typography
                                variant="body1"
                                gutterBottom
                                className={classes.firstSectionField}
                            >
                                Student id:
                                <div className={classes.underline}>
                                    {student.id}
                                </div>
                            </Typography>
                        </div>
                    </div>
                </div>
                <div className={classes.secondSection}>
                    <Typography
                        variant="body1"
                        gutterBottom
                        className={classes.secondSectionField}
                    >
                        How did you hear about us:
                        <div className={classes.underline}>
                            {student.howdidyouhear}
                        </div>
                    </Typography>
                    <Typography
                        variant="body1"
                        gutterBottom
                        className={classes.secondSectionField}
                    >
                        School attending:
                        <div className={classes.underline}>
                            {student.school}
                        </div>
                    </Typography>
                    <Typography
                        variant="body1"
                        gutterBottom
                        className={classes.secondSectionField}
                    >
                        Medical history / concerns:
                        <div className={classes.underline}>
                            {student.medicalhistory}
                        </div>
                    </Typography>
                </div>
                <div className={classes.thirdSection}>
                    <Typography
                        variant="h6"
                        gutterBottom
                        className={classes.subHeader}
                    >
                        Parent / Guardian Information
                    </Typography>
                    <div className={classes.thirdBox}>
                        <Typography
                            variant="body1"
                            gutterBottom
                            className={classes.emptyLeft}
                        >
                            1)
                        </Typography>
                        <div className={classes.flexColumn}>
                            <div className={classes.thirdRow}>
                                <Typography
                                    variant="body1"
                                    gutterBottom
                                    className={classes.thirdSectionField}
                                >
                                    First name:
                                    <div className={classes.underline}>
                                        {student.parent1firstname}
                                    </div>
                                </Typography>
                                <Typography
                                    variant="body1"
                                    gutterBottom
                                    className={classes.thirdSectionField}
                                >
                                    Last name:
                                    <div className={classes.underline}>
                                        {student.parent1lastname}
                                    </div>
                                </Typography>
                            </div>
                            <div className={classes.thirdRow}>
                                <Typography
                                    variant="body1"
                                    gutterBottom
                                    className={classes.thirdSectionField}
                                >
                                    Occupation:
                                    <div className={classes.underline}>
                                        {student.parent1occupation}
                                    </div>
                                </Typography>
                            </div>
                            <div className={classes.thirdRow}>
                                <Typography
                                    variant="body1"
                                    gutterBottom
                                    className={classes.thirdSectionField}
                                >
                                    Email:
                                    <div className={classes.underline}>
                                        {student.parent1email}
                                    </div>
                                </Typography>
                            </div>
                        </div>
                    </div>
                    <div className={classes.thirdBox}>
                        <Typography
                            variant="body1"
                            gutterBottom
                            className={classes.emptyLeft}
                        >
                            2)
                        </Typography>
                        <div className={classes.flexColumn}>
                            <div className={classes.thirdRow}>
                                <Typography
                                    variant="body1"
                                    gutterBottom
                                    className={classes.thirdSectionField}
                                >
                                    First name:
                                    <div className={classes.underline}>
                                        {student.parent2firstname}
                                    </div>
                                </Typography>
                                <Typography
                                    variant="body1"
                                    gutterBottom
                                    className={classes.thirdSectionField}
                                >
                                    Last name:
                                    <div className={classes.underline}>
                                        {student.parent2lastname}
                                    </div>
                                </Typography>
                            </div>
                            <div className={classes.thirdRow}>
                                <Typography
                                    variant="body1"
                                    gutterBottom
                                    className={classes.thirdSectionField}
                                >
                                    Occupation:
                                    <div className={classes.underline}>
                                        {student.parent2occupation}
                                    </div>
                                </Typography>
                            </div>
                            <div className={classes.thirdRow}>
                                <Typography
                                    variant="body1"
                                    gutterBottom
                                    className={classes.thirdSectionField}
                                >
                                    Email:
                                    <div className={classes.underline}>
                                        {student.parent2email}
                                    </div>
                                </Typography>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={classes.fourthSection}>
                    <Typography
                        variant="h6"
                        gutterBottom
                        className={classes.subHeader}
                    >
                        Office use only
                    </Typography>
                    <div className={classes.fourthRow}>
                        <Typography
                            variant="body1"
                            gutterBottom
                            className={classes.fourthSectionField}
                        >
                            Start Date:
                            <div className={classes.underline}>
                                {student.startdate}
                            </div>
                        </Typography>
                        <Typography
                            variant="body1"
                            gutterBottom
                            className={classes.fourthSectionField}
                        >
                            End Date:
                            <div className={classes.underline}>
                                {student.enddate}
                            </div>
                        </Typography>
                        <Typography
                            variant="body1"
                            gutterBottom
                            className={classes.fourthSectionField}
                        >
                            Tuition:
                            <div className={classes.underline}>
                                {student.tuition ? `$ ${student.tuition}` : ''}
                            </div>
                        </Typography>
                    </div>
                    <div className={classes.fourthRow}>
                        <Typography
                            variant="body1"
                            gutterBottom
                            className={classes.fourthSectionField}
                        >
                            Pre-Authorized-Debit:
                            <div className={classes.underline}>
                                {student.preauth ? 'Yes' : 'No'}
                            </div>
                        </Typography>
                        <Typography
                            variant="body1"
                            gutterBottom
                            className={classes.fourthSectionField}
                        >
                            E-Transfer:
                            <div className={classes.underline}>
                                {student.etran ? 'Yes' : 'No'}
                            </div>
                        </Typography>
                        <Typography
                            variant="body1"
                            gutterBottom
                            className={classes.fourthSectionField}
                        >
                            Credit Card:
                            <div className={classes.underline}>
                                {student.creditcard ? 'Yes' : 'No'}
                            </div>
                        </Typography>
                    </div>
                    <div className={classes.fourthRow}>
                        <Typography
                            variant="body1"
                            gutterBottom
                            className={classes.fourthSectionField}
                        >
                            Care Student:
                            <div className={classes.underline}>
                                {student.carestudent ? 'Yes' : 'No'}
                            </div>
                        </Typography>
                        <Typography
                            variant="body1"
                            gutterBottom
                            className={classes.fourthSectionField}
                        >
                            Belt Size:
                            <div className={classes.underline}>
                                {student.beltsize}
                            </div>
                        </Typography>
                    </div>
                </div>
                <div className={classes.noteSection}>
                    <div className={classes.note}>
                        <Typography
                            variant="h6"
                            gutterBottom
                            className={classes.subHeader}
                        >
                            Note
                        </Typography>
                        <Typography
                            variant="body1"
                            display="inline"
                            style={{
                                whiteSpace: 'pre-line',
                                width: '200px',
                                wordBreak: 'break-word',
                            }}
                            noWrap
                            gutterBottom
                        >
                            {student.note}
                        </Typography>
                    </div>
                </div>
                <div className={classes.noteSection}>
                    <div className={classes.note}>
                        <Typography
                            variant="h6"
                            gutterBottom
                            className={classes.subHeader}
                        >
                            Attendance Date
                        </Typography>
                        <Typography variant="body1" gutterBottom>
                            {displayAttendance()}
                        </Typography>
                    </div>
                </div>
                <div>
                    <Button
                        variant="contained"
                        className={classes.button}
                        onClick={() => handlePrintIdCard()}
                    >
                        ID Card
                    </Button>
                    <Button
                        variant="contained"
                        className={classes.button}
                        onClick={() => handlePrintPADForm()}
                    >
                        P.A.D Form
                    </Button>
                    <Button
                        variant="contained"
                        className={classes.button}
                        onClick={() => handlePrintInvoice()}
                    >
                        Invoice
                    </Button>
                </div>
                <div>
                    <Button
                        variant="contained"
                        className={classes.button}
                        onClick={() => history.push('/activestudents')}
                    >
                        BACK
                    </Button>
                    <Button
                        variant="contained"
                        color={student.active ? 'secondary' : 'primary'}
                        className={classes.button}
                        onClick={() => handleActive(student.id, student.active)}
                    >
                        {student.active ? 'Inactive' : 'Active'}
                    </Button>
                    <Button
                        variant="contained"
                        color={'secondary'}
                        className={classes.button}
                        onClick={() => handleDeleteConfirmOpen()}
                    >
                        Delete
                    </Button>
                    <Button
                        variant="contained"
                        color="primary"
                        className={classes.button}
                        onClick={() => handleCopy()}
                    >
                        Copy
                    </Button>
                    <Button
                        variant="contained"
                        color="primary"
                        className={classes.button}
                        onClick={() => handleEdit()}
                    >
                        Edit
                    </Button>
                </div>
            </>
        </div>
    );
}
