import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { getAllStudents, getStudentLastAttendance } from '../helper/db';
import { Typography, Chip } from '@material-ui/core';
import Stack from '@mui/material/Stack';
import { SnackBarState } from './recoilHelper';
import { useRecoilState } from 'recoil';
import { absentTypeCalculator } from '../helper/util';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        padding: '40px',
        flexDirection: 'column',
    },
    subHeader: {
        marginBottom: '10px',
    },
    stack: {
        marginBottom: '40px',
    },
}));

export default function Home() {
    const classes = useStyles();
    const [snackBarState, setSnackBarState] = useRecoilState(SnackBarState);
    const [stats, setStats] = useState({
        week1: [],
        week2: [],
        week3: [],
        more: [],
    });
    const history = useHistory();

    useEffect(() => {
        const getAllAbsents = async () => {
            const tempResult = await getAllStudents();
            if (tempResult === 'fail') {
                setSnackBarState({ ...snackBarState, openError: true });
            }
            const tempStudents = {};
            tempResult.map((student) => {
                tempStudents[student.id] = {
                    name: `${student.firstname} ${student.lastname}`,
                };
            });

            const result = await getStudentLastAttendance();
            if (result === 'fail') {
                setSnackBarState({ ...snackBarState, openError: true });
            }
            const week1 = [];
            const week2 = [];
            const week3 = [];
            const none = [];
            const more = [];
            await result.map((student) => {
                const days = absentTypeCalculator(student.lastdate);
                if (days === '1') {
                    week1.push({
                        ...student,
                        name: tempStudents[student.studentid]?.name,
                    });
                } else if (days === '2') {
                    week2.push({
                        ...student,
                        name: tempStudents[student.studentid]?.name,
                    });
                } else if (days === '3') {
                    week3.push({
                        ...student,
                        name: tempStudents[student.studentid]?.name,
                    });
                } else if (days === 'more') {
                    more.push({
                        ...student,
                        name: tempStudents[student.studentid]?.name,
                    });
                } else if (days === 'none') {
                    none.push({
                        ...student,
                        name: tempStudents[student.studentid]?.name,
                    });
                }
            });
            setStats({ week1, week2, week3, more });
        };
        getAllAbsents();
    }, []);

    const handleNavigate = (studentid) => {
        history.push({ pathname: `/detailstudent/${studentid}` });
    };

    return (
        <div className={classes.root}>
            <Typography variant="h6" gutterBottom className={classes.subHeader}>
                More than 1 week
            </Typography>
            <Stack direction="row" spacing={1} className={classes.stack}>
                {stats.week1.map((student) => {
                    if (student.name) {
                        return (
                            <Chip
                                label={student.name}
                                variant="outlined"
                                onClick={() =>
                                    handleNavigate(student.studentid)
                                }
                            />
                        );
                    }
                })}
            </Stack>
            <Typography variant="h6" gutterBottom className={classes.subHeader}>
                More than 2 weeks
            </Typography>
            <Stack direction="row" spacing={1} className={classes.stack}>
                {stats.week2.map((student) => {
                    if (student.name) {
                        return (
                            <Chip
                                label={student.name}
                                variant="outlined"
                                onClick={() =>
                                    handleNavigate(student.studentid)
                                }
                            />
                        );
                    }
                })}
            </Stack>
            <Typography variant="h6" gutterBottom className={classes.subHeader}>
                More than 3 weeks
            </Typography>
            <Stack direction="row" spacing={1} className={classes.stack}>
                {stats.week3.map((student) => {
                    if (student.name) {
                        return (
                            <Chip
                                label={student.name}
                                variant="outlined"
                                onClick={() =>
                                    handleNavigate(student.studentid)
                                }
                            />
                        );
                    }
                })}
            </Stack>
            <Typography variant="h6" gutterBottom className={classes.subHeader}>
                More
            </Typography>
            <Stack direction="row" spacing={1} className={classes.stack}>
                {stats.more.map((student) => {
                    if (student.name) {
                        return (
                            <Chip
                                label={student.name}
                                variant="outlined"
                                onClick={() =>
                                    handleNavigate(student.studentid)
                                }
                            />
                        );
                    }
                })}
            </Stack>
        </div>
    );
}
