import moment from 'moment';

export function calculateAge(birthday) {
    const age = moment().diff(birthday, 'years');
    return age;
}

export function absentTypeCalculator(date) {
    if (date === '') {
        return 'none';
    }
    const days = moment().diff(date, 'days');
    if (days > 28) {
        return 'more';
    } else if (days > 21) {
        return '3';
    } else if (days > 14) {
        return '2';
    } else if (days > 7) {
        return '1';
    } else {
        return '0';
    }
}
