import React, { useEffect, useRef } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
    Typography,
    Chip,
    Button,
    TextField,
    FormControl,
    FormLabel,
    RadioGroup,
    FormControlLabel,
    Radio,
} from '@material-ui/core';
import { SnackBarState } from './recoilHelper';
import { useRecoilState } from 'recoil';
import QrReader from 'react-qr-scanner';
import { getAllStudents, attendStudents, logging } from '../helper/db';
import moment from 'moment';

const useStyles = makeStyles((theme) => ({
    root: { display: 'flex', flexDirection: 'column', textAlign: 'center' },
    camera: { marginBottom: '20px' },
    status: { marginBottom: '20px' },
    result: { marginBottom: '20px' },
    footer: { marginTop: '10px' },
    chip: { margin: '5px' },
    manualInput: {
        display: 'flex',
        flexDirection: 'row',
        height: '50px',
        textAlign: 'center',
        marginLeft: 'auto',
        marginRight: 'auto',
        marginBottom: '20px',
    },
    manualInputTextField: { marginRight: '5px', width: '300px' },
    manualInputButton: {
        height: '35px',
        marginTop: 'auto',
        marginBottom: 'auto',
    },
    methods: { marginLeft: 'auto', marginRight: 'auto' },
    scannerInputTextField: {
        width: 0,
    },
    dateText: {
        minWidth: '300px',
        maxWidth: '300px',
        margin: '0 auto 20px auto',
    },
}));

export default function Attendance() {
    const classes = useStyles();
    const [snackBarState, setSnackBarState] = useRecoilState(SnackBarState);
    const [qrResult, setQrResult] = React.useState([]);
    const [inputMethod, setInputMethod] = React.useState('scanner');
    const [students, setStudents] = React.useState({});
    const [display, setDisplay] = React.useState('Please scan');
    const [manualStudent, setManualStudent] = React.useState('');
    const [scannerStudent, setScannerStudent] = React.useState('');
    const inputElement = useRef(null);
    const [scannerTimeout, setScannerTimeout] = React.useState('');
    const [date, setDate] = React.useState(moment().format('YYYY-MM-DD'));

    const previewStyle = {
        height: '240px',
        width: '320px',
    };
    const qrDelay = 300;

    useEffect(() => {
        const setAllStudents = async () => {
            const result = await getAllStudents();
            if (result === 'fail') {
                setSnackBarState({ ...snackBarState, openError: true });
            }
            const tempStudents = {};
            result.map((student) => {
                tempStudents[
                    student.id
                ] = `${student.firstname} ${student.lastname}`;
            });
            setStudents(tempStudents);
        };
        setAllStudents();

        if (inputElement.current) {
            inputElement.current.focus();
        }
    }, []);

    const handleScan = (data) => {
        if (data && data.text) {
            const strip = data.text.replace('academyhelper', '');
            let isExist = strip in students;
            let isScanned = false;
            qrResult.map((result) => {
                if (result.id === strip) {
                    isScanned = true;
                }
            });

            if (!isExist) {
                setDisplay(`Student id ${strip} is unknown. Please check.`);
            }
            if (isScanned) {
                setDisplay('Already scanned');
            }
            if (isExist && !isScanned) {
                const temp = [...qrResult];
                temp.push({ id: strip, name: students[strip] });
                setQrResult(temp);
                setDisplay('Scanned successfully');
            }
        }
    };

    const handleError = (err) => {
        console.error(err);
    };

    const handleDelete = (id) => {
        const temp = [];
        qrResult.map((result) => {
            if (result.id !== id) {
                temp.push(result);
            }
        });
        setQrResult(temp);
    };

    const handleChange = (value) => {
        setManualStudent(value);
    };

    const handleManualScan = () => {
        let isExist = manualStudent in students;
        let isScanned = false;
        qrResult.map((result) => {
            if (result.id === manualStudent) {
                isScanned = true;
            }
        });

        if (!isExist) {
            setDisplay(`Student id ${manualStudent} is unknown. Please check.`);
        }
        if (isScanned) {
            setDisplay('Already scanned');
        }
        if (isExist && !isScanned) {
            const temp = [...qrResult];
            temp.push({ id: manualStudent, name: students[manualStudent] });
            setQrResult(temp);
            setDisplay('Scanned successfully');
        }

        setManualStudent('');
    };

    const handleSubmit = async () => {
        const result = await attendStudents(qrResult, date);
        if (result === 'fail') {
            setSnackBarState({ ...snackBarState, openError: true });
        }
        if (result === 'fail2') {
            setSnackBarState({ ...snackBarState, openError: true });
        }
        if (result === 'success') {
            setSnackBarState({ ...snackBarState, openSuccess: true });
            setQrResult([]);
        }
    };

    const handleInputMethodChange = (e) => {
        setInputMethod(e.target.value);
    };

    const handleScannerChange = (value) => {
        //setScannerStudent(value);
        clearTimeout(scannerTimeout);
        setScannerStudent(value);
        const timeoutId = setTimeout(() => {
            handleScannerScan(value);
        }, 500);
        setScannerTimeout(timeoutId);
    };

    const handleScannerScan = async (value) => {
        await logging(value + ' scanned');
        const strip = value.replace('academyhelper', '');
        let isExist = strip in students;
        let isScanned = false;
        qrResult.map((result) => {
            if (result.id === strip) {
                isScanned = true;
            }
        });

        if (!isExist) {
            setDisplay(`Student id ${strip} is unknown. Please check.`);
            await logging(`Student id ${strip} is unknown. Please check.`);
        }
        if (isScanned) {
            setDisplay('Already scanned');
            await logging(`Student id ${strip} is already scanned`);
        }
        if (isExist && !isScanned) {
            const temp = [...qrResult];
            temp.push({ id: strip, name: students[strip] });
            setQrResult(temp);
            setDisplay('Scanned successfully');
        }
        setScannerStudent('');
    };

    const handleDate = (e) => {
        setDate(e.target.value);
    };

    return (
        <div className={classes.root}>
            <TextField
                id="date"
                label="Date"
                type="date"
                InputLabelProps={{
                    shrink: true,
                }}
                onChange={(e) => handleDate(e)}
                value={date}
                className={classes.dateText}
            />
            {inputMethod === 'webcam' && (
                <div className={classes.camera}>
                    <QrReader
                        delay={qrDelay}
                        style={previewStyle}
                        onError={handleError}
                        onScan={handleScan}
                    />
                </div>
            )}
            <Typography variant="h5" gutterBottom className={classes.status}>
                {display}
            </Typography>
            <div className={classes.result}>
                {qrResult.map((result) => {
                    return (
                        <Chip
                            key={result.id}
                            label={result.name}
                            className={classes.chip}
                            onDelete={() => handleDelete(result.id)}
                        />
                    );
                })}
            </div>

            <FormControl component="fieldset" className={classes.methods}>
                <FormLabel component="legend">
                    How would you like to scan?
                </FormLabel>
                <RadioGroup
                    aria-label="methods"
                    name="methods"
                    value={inputMethod}
                    onChange={handleInputMethodChange}
                >
                    <FormControlLabel
                        value="scanner"
                        control={<Radio />}
                        label="QR Code Scanner"
                    />
                    <FormControlLabel
                        value="webcam"
                        control={<Radio />}
                        label="Camera"
                    />
                    <FormControlLabel
                        value="manual"
                        control={<Radio />}
                        label="Manual"
                    />
                </RadioGroup>
            </FormControl>

            {inputMethod === 'scanner' && (
                <TextField
                    ref={inputElement}
                    id="scannerId"
                    value={scannerStudent}
                    onChange={(e) => handleScannerChange(e.target.value)}
                    className={classes.scannerInputTextField}
                    autoFocus={true}
                    onBlur={({ target }) => target.focus()}
                    autoComplete="none"
                />
            )}

            {inputMethod === 'manual' && (
                <div className={classes.manualInput}>
                    <TextField
                        id="id"
                        label="Student ID"
                        value={manualStudent}
                        onChange={(e) => handleChange(e.target.value)}
                        className={classes.manualInputTextField}
                    />
                    <Button
                        variant="contained"
                        color="default"
                        className={classes.manualInputButton}
                        onClick={() => handleManualScan()}
                    >
                        Attend
                    </Button>
                </div>
            )}
            <div className={classes.footer}>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={() => handleSubmit()}
                >
                    Submit
                </Button>
            </div>
        </div>
    );
}
