import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
    FormControl,
    FormLabel,
    RadioGroup,
    FormControlLabel,
    Radio,
    Snackbar,
    Button,
    TextField,
} from '@material-ui/core';
import { SnackBarState } from './recoilHelper';
import { useRecoilState } from 'recoil';
import { getConfigInfo, setConfigInfo, getAdmin, setAdmin } from '../helper/db';
import MuiAlert from '@material-ui/lab/Alert';

function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme) => ({
    root: { display: 'flex', flexDirection: 'column' },
    saveButton: { width: '100px', marginTop: '20px' },
    textField: { width: '200px', marginBottom: '30px' },
    label: { marginBottom: '5px' },
}));

export default function Settings() {
    const classes = useStyles();
    const [snackBarState, setSnackBarState] = useRecoilState(SnackBarState);
    const [beltSetting, setBeltSetting] = useState('a');
    const [open, setOpen] = React.useState(false);
    const [openError, setOpenError] = React.useState(false);
    const [academyName, setAcademyname] = React.useState('');
    const [address, setAddress] = React.useState('');
    const [phone, setPhone] = React.useState('');
    const [username, setUsername] = React.useState('');
    const [passwordinfo, setPassword] = React.useState('');
    const [businessnumber, setBusinessnumber] = React.useState('');
    const [city, setCity] = React.useState('');
    const [province, setProvince] = React.useState('');
    const [email, setEmail] = React.useState('');
    const [postalcode, setPostalcode] = React.useState('');

    useEffect(() => {
        const setConfig = async () => {
            const result = await getConfigInfo();
            if (result === 'fail') {
                setSnackBarState({ ...snackBarState, openError: true });
            }
            setBeltSetting(result[0].beltsetting);
            setAcademyname(result[0].academyname);
            setAddress(result[0].address);
            setPhone(result[0].phone);
            setBusinessnumber(result[0].businessnumber);
            setCity(result[0].city);
            setProvince(result[0].province);
            setPostalcode(result[0].postalcode);
            setEmail(result[0].email);
            const resultAdmin = await getAdmin();
            setUsername(resultAdmin.username);
            setPassword(resultAdmin.passwordinfo);
        };
        setConfig();
    }, []);

    const handleChange = (e) => {
        setBeltSetting(e.target.value);
    };

    const handleSave = async () => {
        const result = await setConfigInfo(
            beltSetting,
            academyName,
            address,
            phone,
            city,
            postalcode,
            province,
            email,
            businessnumber
        );
        const result2 = await setAdmin(username, passwordinfo);
        if (result && result2 === 'success') {
            setOpen(true);
        } else {
            setOpenError(true);
        }
    };

    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpen(false);
    };

    const handleCloseError = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        }
        setOpenError(false);
    };

    return (
        <div className={classes.root}>
            <Snackbar
                open={open}
                autoHideDuration={6000}
                onClose={handleClose}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
                <Alert onClose={handleClose} severity="info">
                    Successfully saved!
                </Alert>
            </Snackbar>
            <Snackbar
                open={openError}
                autoHideDuration={6000}
                onClose={handleCloseError}
                anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
            >
                <Alert onClose={handleCloseError} severity="error">
                    Something went wrong. Please try again.
                </Alert>
            </Snackbar>

            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    User Name
                </FormLabel>
                <TextField
                    id="username"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                    className={classes.textField}
                />
            </FormControl>
            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    Password
                </FormLabel>
                <TextField
                    id="passwordinfo"
                    value={passwordinfo}
                    onChange={(e) => setPassword(e.target.value)}
                    className={classes.textField}
                    type="password"
                />
            </FormControl>

            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    Name of Academy
                </FormLabel>
                <TextField
                    id="academyName"
                    value={academyName}
                    onChange={(e) => setAcademyname(e.target.value)}
                    className={classes.textField}
                />
            </FormControl>
            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    Address
                </FormLabel>
                <TextField
                    id="address"
                    value={address}
                    onChange={(e) => setAddress(e.target.value)}
                    className={classes.textField}
                />
            </FormControl>

            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    City
                </FormLabel>
                <TextField
                    id="city"
                    value={city}
                    onChange={(e) => setCity(e.target.value)}
                    className={classes.textField}
                />
            </FormControl>

            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    Province
                </FormLabel>
                <TextField
                    id="province"
                    value={province}
                    onChange={(e) => setProvince(e.target.value)}
                    className={classes.textField}
                />
            </FormControl>

            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    Postal Code
                </FormLabel>
                <TextField
                    id="postalcode"
                    value={postalcode}
                    onChange={(e) => setPostalcode(e.target.value)}
                    className={classes.textField}
                />
            </FormControl>

            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    Email
                </FormLabel>
                <TextField
                    id="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    className={classes.textField}
                />
            </FormControl>

            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    Busines Number
                </FormLabel>
                <TextField
                    id="businessnumber"
                    value={businessnumber}
                    onChange={(e) => setBusinessnumber(e.target.value)}
                    className={classes.textField}
                />
            </FormControl>

            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    Phone Number
                </FormLabel>
                <TextField
                    id="phone"
                    value={phone}
                    onChange={(e) => setPhone(e.target.value)}
                    className={classes.textField}
                />
            </FormControl>
            <FormControl component="fieldset">
                <FormLabel component="legend" className={classes.label}>
                    Belt Settings
                </FormLabel>
                <RadioGroup
                    aria-label="beltSettings"
                    name="beltSettings"
                    value={beltSetting}
                    onChange={handleChange}
                >
                    <FormControlLabel
                        value="a"
                        control={<Radio />}
                        label="Type A"
                    />
                    <FormControlLabel
                        value="b"
                        control={<Radio />}
                        label="Type B"
                    />
                </RadioGroup>
            </FormControl>

            <Button
                variant="contained"
                color="primary"
                onClick={() => handleSave()}
                className={classes.saveButton}
            >
                Save
            </Button>
        </div>
    );
}
