import React from 'react';
import {
    Button,
    DialogTitle,
    DialogContent,
    DialogContentText,
    DialogActions,
    Dialog,
} from '@material-ui/core';

export default function SaveModal({
    saveHandler,
    handleModalClose,
    open,
    onClose,
}) {
    const handleSave = async () => {
        await saveHandler();
        onClose();
    };

    return (
        <Dialog open={open} onClose={onClose}>
            <DialogTitle id="alert-dialog-title">{'Are you sure?'}</DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    It will save the student information
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button variant="contained" onClick={onClose}>
                    Cancel
                </Button>
                <Button
                    variant="contained"
                    color="primary"
                    onClick={handleSave}
                >
                    Save
                </Button>
            </DialogActions>
        </Dialog>
    );
}
