import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TablePagination,
    TableRow,
    Paper,
    Checkbox,
} from '@material-ui/core';
import {
    getComparator,
    stableSort,
    EnhancedTableToolbarAttendance,
    EnhancedAttendanceTableHead,
    createDataAttendance,
} from './tableHelper';
import {
    deleteAttendance,
    getOnlyStudentsName,
    getAllAttendance,
} from '../helper/db';
import { SnackBarState } from './recoilHelper';
import { useRecoilState } from 'recoil';
import moment from 'moment';
import DeleteModal from './DeleteModal';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    paper: {
        width: 'max-content',
        marginBottom: theme.spacing(2),
    },
    table: {
        minWidth: 750,
    },
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1,
    },
}));

export default function AttendanceTable() {
    const classes = useStyles();
    const [rowsAttendance, setRowsAttendance] = useState([]);

    // table
    const [orderAttendance, setOrderAttendance] = React.useState('desc');
    const [orderByAttendance, setOrderByAttendance] =
        React.useState('lastdate');
    const [selectedAttendance, setSelectedAttendance] = React.useState([]);
    const [pageAttendance, setPageAttendance] = React.useState(0);
    const [rowsPerPageAttendance, setRowsPerPageAttendance] =
        React.useState(30);
    const [snackBarState, setSnackBarState] = useRecoilState(SnackBarState);

    // modal
    const [modalOpen, setModalOpen] = React.useState(false);
    const [modalDeleteId, setModalDeleteId] = React.useState(undefined);

    const handleModalOpen = () => {
        setModalOpen(true);
    };

    const handleModalClose = () => {
        setModalOpen(false);
    };

    const handleDeleteModal = (id) => {
        setModalDeleteId(id);
        handleModalOpen();
    };

    const handleDelete = (id) => async () => {
        const result = await deleteAttendance(id);
        if (result) {
            setSnackBarState({ ...snackBarState, openSuccess: true });
            const tempSelected = [];
            selectedAttendance.map((selectedId) => {
                if (selectedId !== id) {
                    tempSelected.push(selectedId);
                }
            });
            setSelectedAttendance([...tempSelected]);
            refetch();
            handleModalClose();
        } else {
            setSnackBarState({ ...snackBarState, openError: true });
        }
    };

    const refetch = async () => {
        const setAllData = async () => {
            const resultStudentsNames = await getOnlyStudentsName();
            const resultAttendance = await getAllAttendance();
            if (resultStudentsNames === 'fail' || resultAttendance === 'fail') {
                setSnackBarState({ ...snackBarState, openError: true });
                return;
            }

            // merge two tables into one
            const tempStudentsNames = {};
            resultStudentsNames.map((student) => {
                tempStudentsNames[student.id] = {
                    firstname: student.firstname,
                    lastname: student.lastname,
                };
            });

            const tempAttendances = [];
            resultAttendance.map((attendance) => {
                tempAttendances.push({
                    ...attendance,
                    lastdate: moment(attendance.lastdate, 'YYYYMMDD').format(
                        'L'
                    ),
                    ...tempStudentsNames[attendance.studentid],
                });
            });

            // find id and add action buttons
            const tableData = [];
            tempAttendances.map((row) => {
                tableData.push(
                    createDataAttendance(
                        row.id,
                        row.studentid,
                        row.firstname,
                        row.lastname,
                        row.lastdate,
                        <>
                            <Button
                                onClick={() => handleDeleteModal(row.id)}
                                color="secondary"
                            >
                                Delete
                            </Button>
                        </>
                    )
                );
            });
            setRowsAttendance(tableData);
        };
        setAllData();
    };

    useEffect(() => {
        const setAllData = async () => {
            const resultStudentsNames = await getOnlyStudentsName();
            const resultAttendance = await getAllAttendance();
            if (resultStudentsNames === 'fail' || resultAttendance === 'fail') {
                setSnackBarState({ ...snackBarState, openError: true });
                return;
            }

            // merge two tables into one
            const tempStudentsNames = {};
            resultStudentsNames.map((student) => {
                tempStudentsNames[student.id] = {
                    firstname: student.firstname,
                    lastname: student.lastname,
                };
            });

            const tempAttendances = [];
            resultAttendance.map((attendance) => {
                tempAttendances.push({
                    ...attendance,
                    lastdate: moment(attendance.lastdate, 'YYYYMMDD').format(
                        'L'
                    ),
                    ...tempStudentsNames[attendance.studentid],
                });
            });

            // find id and add action buttons
            const tableData = [];
            tempAttendances.map((row) => {
                tableData.push(
                    createDataAttendance(
                        row.id,
                        row.studentid,
                        row.firstname,
                        row.lastname,
                        row.lastdate,
                        <>
                            <Button
                                onClick={() => handleDeleteModal(row.id)}
                                color="secondary"
                            >
                                Delete
                            </Button>
                        </>
                    )
                );
            });
            setRowsAttendance(tableData);
        };
        setAllData();
    }, []);

    const handleRequestSort = (event, property) => {
        const isAsc =
            orderByAttendance === property && orderAttendance === 'asc';
        setOrderAttendance(isAsc ? 'desc' : 'asc');
        setOrderByAttendance(property);
    };

    const handleSelectAllClick = (event) => {
        if (event.target.checked) {
            const newSelecteds = rowsAttendance.map((n) => n.id);
            setSelectedAttendance(newSelecteds);
            return;
        }
        setSelectedAttendance([]);
    };

    const handleClick = (event, id) => {
        const selectedIndex = selectedAttendance.indexOf(id);
        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selectedAttendance, id);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selectedAttendance.slice(1));
        } else if (selectedIndex === selectedAttendance.length - 1) {
            newSelected = newSelected.concat(selectedAttendance.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                selectedAttendance.slice(0, selectedIndex),
                selectedAttendance.slice(selectedIndex + 1)
            );
        }

        setSelectedAttendance(newSelected);
    };

    const handleChangePage = (event, newPage) => {
        setPageAttendance(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPageAttendance(parseInt(event.target.value, 10));
        setPageAttendance(0);
    };

    const isSelected = (id) => selectedAttendance.indexOf(id) !== -1;

    const emptyRows =
        rowsPerPageAttendance -
        Math.min(
            rowsPerPageAttendance,
            rowsAttendance.length - pageAttendance * rowsPerPageAttendance
        );

    return (
        <div className={classes.root}>
            <Paper className={classes.paper}>
                <EnhancedTableToolbarAttendance
                    numSelected={selectedAttendance.length}
                    selected={selectedAttendance}
                    setSnackBarState={setSnackBarState}
                    snackBarState={snackBarState}
                    refetch={refetch}
                    setSelected={setSelectedAttendance}
                />
                <DeleteModal
                    open={modalOpen}
                    onClose={handleModalClose}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                    deleteHandler={handleDelete(modalDeleteId)}
                />
                <TableContainer>
                    <Table
                        className={classes.table}
                        aria-labelledby="tableTitle"
                        size={'medium'}
                        aria-label="enhanced table"
                    >
                        <EnhancedAttendanceTableHead
                            classes={classes}
                            numSelected={selectedAttendance.length}
                            order={orderAttendance}
                            orderBy={orderByAttendance}
                            onSelectAllClick={handleSelectAllClick}
                            onRequestSort={handleRequestSort}
                            rowCount={rowsAttendance.length}
                        />
                        <TableBody>
                            {stableSort(
                                rowsAttendance,
                                getComparator(
                                    orderAttendance,
                                    orderByAttendance
                                )
                            )
                                .slice(
                                    pageAttendance * rowsPerPageAttendance,
                                    pageAttendance * rowsPerPageAttendance +
                                        rowsPerPageAttendance
                                )
                                .map((row, index) => {
                                    const isItemSelected = isSelected(row.id);
                                    const labelId = `enhanced-table-checkbox-${index}`;

                                    return (
                                        <TableRow
                                            hover
                                            role="checkbox"
                                            aria-checked={isItemSelected}
                                            tabIndex={-1}
                                            key={Math.floor(
                                                Math.random() * 10000000
                                            )}
                                            selected={isItemSelected}
                                        >
                                            <TableCell padding="checkbox">
                                                <Checkbox
                                                    checked={isItemSelected}
                                                    inputProps={{
                                                        'aria-labelledby':
                                                            labelId,
                                                    }}
                                                    onClick={(event) =>
                                                        handleClick(
                                                            event,
                                                            row.id
                                                        )
                                                    }
                                                />
                                            </TableCell>
                                            <TableCell
                                                component="th"
                                                id={labelId}
                                                scope="row"
                                                padding="none"
                                            >
                                                {row.id}
                                            </TableCell>
                                            <TableCell align="left">
                                                {row.studentid}
                                            </TableCell>
                                            <TableCell align="left">
                                                {row.firstname}
                                            </TableCell>
                                            <TableCell align="left">
                                                {row.lastname}
                                            </TableCell>
                                            <TableCell align="left">
                                                {row.lastdate}
                                            </TableCell>
                                            <TableCell align="left">
                                                {row.action}
                                            </TableCell>
                                        </TableRow>
                                    );
                                })}
                            {emptyRows > 0 && (
                                <TableRow
                                    style={{
                                        height: 53 * emptyRows,
                                    }}
                                >
                                    <TableCell colSpan={6} />
                                </TableRow>
                            )}
                        </TableBody>
                    </Table>
                </TableContainer>
                <TablePagination
                    rowsPerPageOptions={[15, 30, 50]}
                    component="div"
                    count={rowsAttendance.length}
                    rowsPerPage={rowsPerPageAttendance}
                    page={pageAttendance}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
            </Paper>
        </div>
    );
}
