import React, { useRef, useState, useEffect } from 'react';
import { Button } from '@material-ui/core';
import { useReactToPrint } from 'react-to-print';
import ComponentToPrint from './ComponentToPrint';
import { useHistory, useParams } from 'react-router-dom';
import { getStudent, getPicture, getConfigInfo } from '../helper/db';

const root = {
    display: 'flex',
    flexDirection: 'column',
    '@media print': {
        display: 'block',
    },
};

const divButton = {
    marginLeft: '40px',
    marginRight: 'auto',
};

const button = {
    width: 'fit-content',
    marginLeft: '5px',
    marginRight: '5px',
};

const IdCard = () => {
    const componentRef = useRef();

    const pageStyle = `
        @page {
            size: 102mm 228mm;
        }
  `;
    const handlePrint = useReactToPrint({
        content: () => componentRef.current,
        pageStyle: pageStyle,
    });
    const [curStudent, setCurStudent] = useState({});
    const [picture, setPicture] = useState('');
    const history = useHistory();
    const { id } = useParams();
    const [beltSetting, setBeltSetting] = React.useState('a');

    useEffect(() => {
        const setStudentCall = async () => {
            const result = await getStudent(id);
            if (!result) {
                history.push('/error');
                return;
            }
            setCurStudent(result);
            const resultPicture = await getPicture(id);
            if (!resultPicture) {
                history.push('/error');
                return;
            }
            if (resultPicture?.length > 0) {
                setPicture(resultPicture[0].picture);
            }
            const resultConfig = await getConfigInfo();
            setBeltSetting(resultConfig[0].beltsetting);
        };
        setStudentCall();
    }, []);

    return (
        <div style={root}>
            <ComponentToPrint
                ref={componentRef}
                curStudent={curStudent}
                picture={picture}
                beltSetting={beltSetting}
            />
            <div style={divButton}>
                <Button
                    onClick={() => history.push(`/detailstudent/${id}`)}
                    variant="contained"
                    style={button}
                >
                    Back
                </Button>
                <Button
                    onClick={handlePrint}
                    variant="contained"
                    color="primary"
                    style={button}
                >
                    PRINT
                </Button>
            </div>
        </div>
    );
};

export default IdCard;
