import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { getNumStudents, getConfigInfo, getNumBelts } from '../helper/db';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import { beltsA, beltsB, beltsClassA, beltsClassB } from '../helper/constant';
import { Typography } from '@material-ui/core';
import { useHistory } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        padding: '40px',
        flexDirection: 'column',
    },
    table: { width: '400px', marginBottom: '60px' },
}));

const createData = (name, num, code) => {
    return {
        name,
        num,
        code,
    };
};

export default function Home() {
    const classes = useStyles();
    const [stats, setStats] = useState({
        totalNum: 0,
        belt: {
            class1: { b01: 0, b02: 0, b03: 0, b04: 0, b05: 0, b06: 0 },
            class2: {
                b07: 0,
                b08: 0,
                b09: 0,
                b10: 0,
                b11: 0,
                b12: 0,
                b13: 0,
                b14: 0,
                b15: 0,
                b16: 0,
            },
            class3: {
                b17: 0,
                b18: 0,
                b19: 0,
                b20: 0,
                b21: 0,
                b22: 0,
            },
            class4: {
                b23: 0,
                b24: 0,
                b25: 0,
                b26: 0,
                b27: 0,
                b28: 0,
                b29: 0,
                b30: 0,
                b31: 0,
            },
            class5: {
                b32: 0,
                b33: 0,
                b34: 0,
                b35: 0,
                b36: 0,
                b37: 0,
                b38: 0,
                b39: 0,
                b40: 0,
                b41: 0,
                b42: 0,
                b43: 0,
            },
        },
    });
    const [beltSetting, setBeltSetting] = React.useState('a');
    const history = useHistory();

    useEffect(() => {
        const getAllStats = async () => {
            const result = await getConfigInfo();
            setBeltSetting(result[0].beltsetting);

            const beltSetting = result[0]?.beltsetting;
            let beltClass;
            if (beltSetting === 'a') {
                beltClass = beltsClassA;
            } else {
                beltClass = beltsClassB;
            }

            const temp = {
                class1: {},
                class2: {},
                class3: {},
                class4: {},
                class5: {},
                total1: 0,
                total2: 0,
                total3: 0,
                total4: 0,
                total5: 0,
            };

            const numBeltsResult = await getNumBelts();
            const numBelts = {};
            numBeltsResult.map((m) => (numBelts[m.belt] = m.count));

            beltClass.class1.map((belt) => {
                const beltCount = numBelts.hasOwnProperty(belt)
                    ? numBelts[belt]
                    : '0';
                temp.class1[belt] = {
                    count: beltCount,
                    code: belt,
                };
                temp.total1 += parseInt(beltCount);
            });
            beltClass.class2.map(async (belt) => {
                const beltCount = numBelts.hasOwnProperty(belt)
                    ? numBelts[belt]
                    : '0';
                temp.class2[belt] = {
                    count: beltCount,
                    code: belt,
                };
                temp.total2 += parseInt(beltCount);
            });
            beltClass.class3.map(async (belt) => {
                const beltCount = numBelts.hasOwnProperty(belt)
                    ? numBelts[belt]
                    : '0';
                temp.class3[belt] = {
                    count: beltCount,
                    code: belt,
                };
                temp.total3 += parseInt(beltCount);
            });
            beltClass.class4.map(async (belt) => {
                const beltCount = numBelts.hasOwnProperty(belt)
                    ? numBelts[belt]
                    : '0';
                temp.class4[belt] = {
                    count: beltCount,
                    code: belt,
                };
                temp.total4 += parseInt(beltCount);
            });
            beltClass.class5.map(async (belt) => {
                const beltCount = numBelts.hasOwnProperty(belt)
                    ? numBelts[belt]
                    : '0';
                temp.class5[belt] = {
                    count: beltCount,
                    code: belt,
                };
                temp.total5 += parseInt(beltCount);
            });
            const numStudents = await getNumStudents();
            setStats({
                totalNum: numStudents,
                belt: {
                    ...temp,
                },
            });
        };
        getAllStats();
    }, []);

    let belts;
    if (beltSetting === 'a') {
        belts = beltsA;
    } else {
        belts = beltsB;
    }

    console.log('***', stats);
    const rows1 = Object.entries(stats.belt.class1)
        .sort((a, b) => (a[1].code > b[1].code ? 1 : -1))
        .map((belt) => createData(belts[belt[0]], belt[1].count, belt[1].code));
    const rows2 = Object.entries(stats.belt.class2)
        .sort((a, b) => (a[1].code > b[1].code ? 1 : -1))
        .map((belt) => createData(belts[belt[0]], belt[1].count, belt[1].code));
    const rows3 = Object.entries(stats.belt.class3)
        .sort((a, b) => (a[1].code > b[1].code ? 1 : -1))
        .map((belt) => createData(belts[belt[0]], belt[1].count, belt[1].code));
    const rows4 = Object.entries(stats.belt.class4)
        .sort((a, b) => (a[1].code > b[1].code ? 1 : -1))
        .map((belt) => createData(belts[belt[0]], belt[1].count, belt[1].code));
    const rows5 = Object.entries(stats.belt.class5)
        .sort((a, b) => (a[1].code > b[1].code ? 1 : -1))
        .map((belt) => createData(belts[belt[0]], belt[1].count, belt[1].code));

    const handleNavigate = (belt) => {
        history.push({ pathname: `/activestudentsbybelt/${belt}` });
    };

    return (
        <div className={classes.root}>
            <Typography variant="h6" gutterBottom className={classes.subHeader}>
                {`Total Number of Students: ${stats.totalNum.count}`}
            </Typography>
            <TableContainer>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Belt Color</TableCell>
                            <TableCell align="right">
                                Number of Students
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows1.map((row) => (
                            <TableRow
                                key={row.name}
                                onClick={() => handleNavigate(row.code)}
                            >
                                <TableCell component="th" scope="row">
                                    {row.name}
                                </TableCell>
                                <TableCell align="right">{row.num}</TableCell>
                            </TableRow>
                        ))}
                        <TableRow>
                            <TableCell component="th" scope="row">
                                <b>Total Students</b>
                            </TableCell>
                            <TableCell align="right">
                                <b>{stats?.belt?.total1}</b>
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>

                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Belt Color</TableCell>
                            <TableCell align="right">
                                Number of Students
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows2.map((row) => (
                            <TableRow
                                key={row.name}
                                onClick={() => handleNavigate(row.code)}
                            >
                                <TableCell component="th" scope="row">
                                    {row.name}
                                </TableCell>
                                <TableCell align="right">{row.num}</TableCell>
                            </TableRow>
                        ))}
                        <TableRow>
                            <TableCell component="th" scope="row">
                                <b>Total Students</b>
                            </TableCell>
                            <TableCell align="right">
                                <b>{stats?.belt?.total2}</b>
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>

                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Belt Color</TableCell>
                            <TableCell align="right">
                                Number of Students
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows3.map((row) => (
                            <TableRow
                                key={row.name}
                                onClick={() => handleNavigate(row.code)}
                            >
                                <TableCell component="th" scope="row">
                                    {row.name}
                                </TableCell>
                                <TableCell align="right">{row.num}</TableCell>
                            </TableRow>
                        ))}
                        <TableRow>
                            <TableCell component="th" scope="row">
                                <b>Total Students</b>
                            </TableCell>
                            <TableCell align="right">
                                <b>{stats?.belt?.total3}</b>
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>

                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Belt Color</TableCell>
                            <TableCell align="right">
                                Number of Students
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows4.map((row) => (
                            <TableRow
                                key={row.name}
                                onClick={() => handleNavigate(row.code)}
                            >
                                <TableCell component="th" scope="row">
                                    {row.name}
                                </TableCell>
                                <TableCell align="right">{row.num}</TableCell>
                            </TableRow>
                        ))}
                        <TableRow>
                            <TableCell component="th" scope="row">
                                <b>Total Students</b>
                            </TableCell>
                            <TableCell align="right">
                                <b>{stats?.belt?.total4}</b>
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>

                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Belt Color</TableCell>
                            <TableCell align="right">
                                Number of Students
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows5.map((row) => (
                            <TableRow
                                key={row.name}
                                onClick={() => handleNavigate(row.code)}
                            >
                                <TableCell component="th" scope="row">
                                    {row.name}
                                </TableCell>
                                <TableCell align="right">{row.num}</TableCell>
                            </TableRow>
                        ))}
                        <TableRow>
                            <TableCell component="th" scope="row">
                                <b>Total Students</b>
                            </TableCell>
                            <TableCell align="right">
                                <b>{stats?.belt?.total5}</b>
                            </TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    );
}
