import React from 'react';

import Login from './component/Login';
import Base from './component/Base';
import AddStudent from './component/Adding/AddStudent';
import ActiveStudents from './component/ActiveStudents';
import DetailStudent from './component/DetailStudent';
import CopyStudent from './component/Adding/CopyStudent';
import EditStudent from './component/Adding/EditStudent';
import ErrorPage from './component/ErrorPage';
import IdCard from './print/IdCard';
import Attendance from './component/Attendance';
import AttendanceKeypad from './component/AttendanceKeypad';
import AttendanceTable from './component/AttendanceTable';
import InactiveStudents from './component/InactiveStudents';
import Absent from './component/Absent';
import Home from './component/Home';
import TakePicture from './component/TakePicture';
import { RecoilRoot } from 'recoil';

import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Settings from './component/Settings';
import BeltUpdate from './component/BeltUpdate';
import ActiveStudentsByBelt from './component/ActiveStudentsByBelt';
import PadForm from './print/PadForm';
import Invoice from './print/Invoice';

function App() {
    const basePath = '/academyhelper';
    return (
        <Router>
            <RecoilRoot>
                <div>
                    <Switch>
                        <Route path={`${basePath}/signin`}>
                            <Login />
                        </Route>
                        <Route path={`${basePath}/addstudent`}>
                            <Base title="Add student">
                                <AddStudent />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/activestudents`}>
                            <Base title="Active students">
                                <ActiveStudents />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/activestudentsByBelt/:belt`}>
                            <Base title="Active Students by Belt">
                                <ActiveStudentsByBelt />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/detailstudent/:id`}>
                            <Base title="Student detail">
                                <DetailStudent />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/copystudent`}>
                            <Base title="Add student">
                                <CopyStudent />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/editstudent/:id`}>
                            <Base title="Edit student">
                                <EditStudent />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/printidcard/:id`}>
                            <Base title="Print ID Card">
                                <IdCard />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/printpadform/:id`}>
                            <Base title="Print PAD Form">
                                <PadForm />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/printinvoice/:id`}>
                            <Base title="Print Invoice">
                                <Invoice />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/attendance`}>
                            <Base title="Attendance">
                                <Attendance />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/attendancekeypad`}>
                            <AttendanceKeypad />
                        </Route>
                        <Route path={`${basePath}/attendancetable`}>
                            <Base title="Attendance Table">
                                <AttendanceTable />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/inactivestudents`}>
                            <Base title="Inactive students">
                                <InactiveStudents />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/takepicture/:id`}>
                            <Base title="Take Picture">
                                <TakePicture />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/settings`}>
                            <Base title="Settings">
                                <Settings />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/beltupdate`}>
                            <Base title="Belt Update">
                                <BeltUpdate />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/absent`}>
                            <Base title="Absent Students">
                                <Absent />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/error`}>
                            <Base title="Error">
                                <ErrorPage />
                            </Base>
                        </Route>
                        <Route path={`${basePath}/home`}>
                            <Base title="Dashboard">
                                <Home />
                            </Base>
                        </Route>
                        <Route path={`${basePath}`}>
                            <Base title="Dashboard">
                                <Home />
                            </Base>
                        </Route>
                    </Switch>
                </div>
            </RecoilRoot>
        </Router>
    );
}

export default App;
