import { atom, selector } from 'recoil';

// get / set for id
export const IdState = atom({
    key: 'id',
    default: 0,
});

export const GetIdState = selector({
    key: 'getIdState',
    get: ({ get }) => {
        const id = get(IdState);
        return id;
    },
});

// get / set for edit - student information
export const CurStudentState = atom({
    key: 'curStudent',
    default: { isDone: false },
});

export const GetCurStudentState = selector({
    key: 'getCurStudentState',
    get: ({ get }) => {
        const curStudent = get(CurStudentState);
        return curStudent;
    },
});

// snack bar
export const SnackBarState = atom({
    key: 'snackBarState',
    default: {
        openSuccess: false,
        openError: false,
        openError2: false,
        openErrorField: false,
    },
});

export const GetSnackBarState = selector({
    key: 'getSnackBarState',
    get: ({ get }) => {
        const snackBarState = get(SnackBarState);
        return snackBarState;
    },
});
