const useToolbarStyles = makeStyles((theme) => ({
    root: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(1),
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                  color: theme.palette.secondary.main,
                  backgroundColor: lighten(theme.palette.secondary.light, 0.85),
              }
            : {
                  color: theme.palette.text.primary,
                  backgroundColor: theme.palette.secondary.dark,
              },
    title: {
        flex: '1 1 100%',
    },
}));

export const EnhancedTableToolbar = (props) => {
    const classes = useToolbarStyles();
    const {
        numSelected,
        selected,
        setSnackBarState,
        refetch,
        snackBarState,
        setSelected,
    } = props;

    const handleDeleteAll = async () => {
        const selectedIds = selected.join(',');
        const result = await deleteStudents(selectedIds);
        if (result) {
            setSnackBarState({ ...snackBarState, openSuccess: true });
            setSelected([]);
            refetch();
        } else {
            setSnackBarState({ ...snackBarState, openError: true });
            refetch();
        }
    };

    return (
        numSelected > 0 && (
            <Toolbar
                className={clsx(classes.root, {
                    [classes.highlight]: numSelected > 0,
                })}
            >
                {numSelected > 0 && (
                    <Typography
                        className={classes.title}
                        color="inherit"
                        variant="subtitle1"
                        component="div"
                    >
                        {numSelected} selected
                    </Typography>
                )}

                {numSelected > 0 ? (
                    <Tooltip title="Delete">
                        <IconButton aria-label="delete">
                            <DeleteIcon onClick={() => handleDeleteAll()} />
                        </IconButton>
                    </Tooltip>
                ) : (
                    <></>
                )}
            </Toolbar>
        )
    );
};

