import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableRow,
    Paper,
    Checkbox,
} from '@material-ui/core';
import {
    createData,
    getComparator,
    stableSort,
    EnhancedTableToolbar,
    EnhancedTableHead,
    dobYearRemover,
} from './tableHelper';
import {
    getAllStudentsByBelt,
    deleteStudent,
    getConfigInfo,
} from '../helper/db';
import { useHistory, useParams } from 'react-router-dom';
import { IdState, SnackBarState } from './recoilHelper';
import { useRecoilState } from 'recoil';
import DeleteModal from './DeleteModal';
import { beltMapper } from '../helper/constant';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        maxHeight: 'calc(100vh - 150px)',
        overflowX: 'scroll',
    },
    paper: {
        width: 'max-content',
        marginBottom: theme.spacing(2),
    },
    table: {
        minWidth: 750,
    },
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1,
    },
}));

export default function ActiveStudentsByBelt() {
    const classes = useStyles();
    const [rows, setRows] = useState([]);
    const [id, setId] = useRecoilState(IdState);
    const history = useHistory();
    const { belt } = useParams();

    // table
    const [order, setOrder] = React.useState('asc');
    const [orderBy, setOrderBy] = React.useState('id');
    const [selected, setSelected] = React.useState([]);
    const [page, setPage] = React.useState(0);
    // const [rowsPerPage, setRowsPerPage] = React.useState(15);
    const [snackBarState, setSnackBarState] = useRecoilState(SnackBarState);

    // modal
    const [modalOpen, setModalOpen] = React.useState(false);
    const [modalDeleteId, setModalDeleteId] = React.useState(undefined);

    // belt
    const [beltSetting, setBeltSetting] = React.useState('a');

    const handleModalOpen = () => {
        setModalOpen(true);
    };

    const handleModalClose = () => {
        setModalOpen(false);
    };

    const handleNavigate = (id) => {
        setId(id);
        history.push({ pathname: `/detailstudent/${id}` });
    };

    const handleEdit = (id) => {
        setId(id);
        history.push(`/editstudent/${id}`);
    };

    const handleDeleteModal = (id) => {
        setModalDeleteId(id);
        handleModalOpen();
    };

    const handleDelete = (id) => async () => {
        const result = await deleteStudent(id);
        if (result) {
            setSnackBarState({ ...snackBarState, openSuccess: true });
            const tempSelected = [];
            selected.map((selectedId) => {
                if (selectedId !== id) {
                    tempSelected.push(selectedId);
                }
            });
            setSelected([...tempSelected]);
            refetch();
            handleModalClose();
        } else {
            setSnackBarState({ ...snackBarState, openError: true });
        }
    };

    const refetch = async () => {
        const resultBelt = await getConfigInfo();
        if (resultBelt === 'fail') {
            setSnackBarState({ ...snackBarState, openError: true });
        }
        setBeltSetting(resultBelt[0].beltsetting);
        const orderByBelt = localStorage.getItem('orderByBelt');
        const orderBy_ = orderByBelt ? JSON.parse(orderByBelt).orderBy : 'id';
        const orderType_ = orderByBelt
            ? JSON.parse(orderByBelt).orderType
            : 'asc';
        setOrderBy(orderBy_);
        setOrder(orderType_);

        const result = await getAllStudentsByBelt(belt);
        if (result === 'fail') {
            setSnackBarState({ ...snackBarState, openError: true });
        }
        // find id and add action buttons
        const tableData = [];
        result.map((row) => {
            tableData.push(
                createData(
                    row.id,
                    row.firstname,
                    row.lastname,
                    row.belt,
                    row.beltsize,
                    row.lastbelttest,
                    row.phonecell,
                    row.enddate,
                    dobYearRemover(row.dateofbirth),
                    <>
                        <Button onClick={() => handleNavigate(row.id)}>
                            View
                        </Button>
                        <Button onClick={() => handleEdit(row.id)}>Edit</Button>
                        <Button
                            onClick={() => handleDeleteModal(row.id)}
                            color="secondary"
                        >
                            Delete
                        </Button>
                    </>,
                    row.carestudent
                )
            );
        });
        setRows(tableData);
    };

    useEffect(() => {
        const setAllStudents = async () => {
            const resultBelt = await getConfigInfo();
            if (resultBelt === 'fail') {
                setSnackBarState({ ...snackBarState, openError: true });
            }
            setBeltSetting(resultBelt[0].beltsetting);
            const orderByBelt = localStorage.getItem('orderByBelt');
            const orderBy_ = orderByBelt
                ? JSON.parse(orderByBelt).orderBy
                : 'id';
            const orderType_ = orderByBelt
                ? JSON.parse(orderByBelt).orderType
                : 'asc';
            setOrderBy(orderBy_);
            setOrder(orderType_);

            const result = await getAllStudentsByBelt(belt);
            if (result === 'fail') {
                setSnackBarState({ ...snackBarState, openError: true });
            }
            // find id and add action buttons
            const tableData = [];
            result.map((row) => {
                tableData.push(
                    createData(
                        row.id,
                        row.firstname,
                        row.lastname,
                        row.belt,
                        row.beltsize,
                        row.lastbelttest,
                        row.phonecell,
                        row.enddate,
                        dobYearRemover(row.dateofbirth),
                        <>
                            <Button onClick={() => handleNavigate(row.id)}>
                                View
                            </Button>
                            <Button onClick={() => handleEdit(row.id)}>
                                Edit
                            </Button>
                            <Button
                                onClick={() => handleDeleteModal(row.id)}
                                color="secondary"
                            >
                                Delete
                            </Button>
                        </>,
                        row.carestudent
                    )
                );
            });
            setRows(tableData);
        };
        setAllStudents();
    }, []);

    const handleRequestSort = async (event, property) => {
        const isAsc = orderBy === property && order === 'asc';
        setOrder(isAsc ? 'desc' : 'asc');
        localStorage.setItem(
            'orderByBelt',
            JSON.stringify({
                orderBy: property,
                orderType: isAsc ? 'desc' : 'asc',
            })
        );
        setOrderBy(property);
    };

    const handleSelectAllClick = (event) => {
        if (event.target.checked) {
            const newSelecteds = rows.map((n) => n.id);
            setSelected(newSelecteds);
            return;
        }
        setSelected([]);
    };

    const handleClick = (event, id) => {
        const selectedIndex = selected.indexOf(id);
        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selected, id);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
            newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                selected.slice(0, selectedIndex),
                selected.slice(selectedIndex + 1)
            );
        }

        setSelected(newSelected);
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    // const handleChangeRowsPerPage = (event) => {
    //     setRowsPerPage(parseInt(event.target.value, 10));
    //     setPage(0);
    // };

    const isSelected = (id) => selected.indexOf(id) !== -1;

    // const emptyRows =
    //     rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage);

    return (
        <div className={classes.root}>
            <Paper className={classes.paper}>
                <EnhancedTableToolbar
                    numSelected={selected.length}
                    selected={selected}
                    setSnackBarState={setSnackBarState}
                    snackBarState={snackBarState}
                    refetch={refetch}
                    setSelected={setSelected}
                />
                <DeleteModal
                    open={modalOpen}
                    onClose={handleModalClose}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                    deleteHandler={handleDelete(modalDeleteId)}
                />
                <TableContainer>
                    <Table
                        className={classes.table}
                        aria-labelledby="tableTitle"
                        size={'medium'}
                        aria-label="enhanced table"
                    >
                        <EnhancedTableHead
                            classes={classes}
                            numSelected={selected.length}
                            order={order}
                            orderBy={orderBy}
                            onSelectAllClick={handleSelectAllClick}
                            onRequestSort={handleRequestSort}
                            rowCount={rows.length}
                        />
                        <TableBody>
                            {stableSort(
                                rows,
                                getComparator(order, orderBy)
                            ).map((row, index) => {
                                const isItemSelected = isSelected(row.id);
                                const labelId = `enhanced-table-checkbox-${index}`;

                                return (
                                    <TableRow
                                        hover
                                        role="checkbox"
                                        aria-checked={isItemSelected}
                                        tabIndex={-1}
                                        key={row.id}
                                        selected={isItemSelected}
                                    >
                                        <TableCell padding="checkbox">
                                            <Checkbox
                                                checked={isItemSelected}
                                                inputProps={{
                                                    'aria-labelledby': labelId,
                                                }}
                                                onClick={(event) =>
                                                    handleClick(event, row.id)
                                                }
                                            />
                                        </TableCell>
                                        <TableCell
                                            component="th"
                                            id={labelId}
                                            scope="row"
                                            padding="none"
                                            onClick={() =>
                                                handleNavigate(row.id)
                                            }
                                        >
                                            {row.id}
                                        </TableCell>
                                        <TableCell
                                            align="left"
                                            onClick={() =>
                                                handleNavigate(row.id)
                                            }
                                        >
                                            <span
                                                style={
                                                    row.carestudent
                                                        ? {
                                                              backgroundColor:
                                                                  'yellow',
                                                          }
                                                        : {}
                                                }
                                            >
                                                {row.firstname}
                                            </span>
                                        </TableCell>
                                        <TableCell
                                            align="left"
                                            onClick={() =>
                                                handleNavigate(row.id)
                                            }
                                        >
                                            <span
                                                style={
                                                    row.carestudent
                                                        ? {
                                                              backgroundColor:
                                                                  'yellow',
                                                          }
                                                        : {}
                                                }
                                            >
                                                {row.lastname}
                                            </span>
                                        </TableCell>
                                        <TableCell
                                            align="left"
                                            onClick={() =>
                                                handleNavigate(row.id)
                                            }
                                        >
                                            {row.belt === 'undefined'
                                                ? ''
                                                : beltMapper(
                                                      row.belt,
                                                      beltSetting
                                                  )}
                                        </TableCell>
                                        <TableCell
                                            align="left"
                                            onClick={() =>
                                                handleNavigate(row.id)
                                            }
                                        >
                                            {row.beltsize === 'undefined'
                                                ? ''
                                                : row.beltsize}
                                        </TableCell>
                                        <TableCell
                                            align="left"
                                            onClick={() =>
                                                handleNavigate(row.id)
                                            }
                                        >
                                            {row.lastbelttest === 'undefined'
                                                ? ''
                                                : row.lastbelttest}
                                        </TableCell>
                                        <TableCell
                                            align="left"
                                            onClick={() =>
                                                handleNavigate(row.id)
                                            }
                                        >
                                            {row.phonecell === 'undefined'
                                                ? ''
                                                : row.phonecell}
                                        </TableCell>
                                        <TableCell
                                            align="left"
                                            onClick={() =>
                                                handleNavigate(row.id)
                                            }
                                        >
                                            {row.enddate === 'undefined'
                                                ? ''
                                                : row.enddate}
                                        </TableCell>
                                        <TableCell
                                            align="left"
                                            onClick={() =>
                                                handleNavigate(row.id)
                                            }
                                        >
                                            {row.dateofbirth === 'undefined'
                                                ? ''
                                                : row.dateofbirth}
                                        </TableCell>
                                        <TableCell align="left">
                                            {row.action}
                                        </TableCell>
                                    </TableRow>
                                );
                            })}
                            {/* {emptyRows > 0 && (
                                <TableRow
                                    style={{
                                        height: 53 * emptyRows,
                                    }}
                                >
                                    <TableCell colSpan={6} />
                                </TableRow>
                            )} */}
                        </TableBody>
                    </Table>
                </TableContainer>
                {/* <TablePagination
                    rowsPerPageOptions={[15, 30, 50]}
                    component="div"
                    count={rows.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                /> */}
            </Paper>
        </div>
    );
}
