import React from 'react';
import {
    Button,
    DialogTitle,
    DialogContent,
    DialogContentText,
    DialogActions,
    Dialog,
} from '@material-ui/core';

export default function DeleteModal({
    deleteHandler,
    handleModalClose,
    open,
    onClose,
}) {
    const handleDelete = async () => {
        await deleteHandler();
    };

    return (
        <Dialog open={open} onClose={onClose}>
            <DialogTitle id="alert-dialog-title">{'Are you sure?'}</DialogTitle>
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    It will delete the student and you can't restore.
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button variant="contained" onClick={onClose}>
                    Cancel
                </Button>
                <Button
                    variant="contained"
                    color="secondary"
                    onClick={handleDelete}
                >
                    DELETE
                </Button>
            </DialogActions>
        </Dialog>
    );
}
