import React from 'react';
import QRCode from 'react-qr-code';
import {
    beltsClassMapperA,
    beltsClassMapperB,
    beltMapper,
    beltColorMapper,
} from '../helper/constant';
import FormControl from '@material-ui/core/FormControl';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

const Style = {
    renderPage: {
        width: '100%',
        height: '100%',
        fontSize: '16px',
    },
    renderCard: {
        width: '600px',
        height: '100%',
        textAlign: 'center',
        margin: '0 auto 0 auto',
        display: 'flow-root',
        padding: '50px',
    },
    renderText: {
        fontSize: '25px',
        fontWeight: '500',
        marginBottom: '5px',
    },
    formControl: {
        margin: '10px',
        display: 'flex',
        flexDirection: 'column',
    },
    beltRow: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        marginBottom: '10px',
    },
    capturedImage: {
        width: '216px',
        height: '264px',
        margin: '0 auto 15px auto',
    },
    sectionContainer: {
        display: 'flex',
        flexDirection: 'row',
        gap: '25px',
        justifyContent: 'center',
        marginTop: '20px',
        marginBottom: '20px',
    },
    section: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-evenly',
        maxWidth: '200px',
    },
    checkbox: {
        height: '30px',
        width: '30px',
    },
    wrap: {
        display: 'flex',
        flexDirection: 'column',
        flexWrap: 'wrap',
        height: '520px',
        marginTop: '80px',
    },
};

const beltBackground = (color1, color2, color3) => {
    if (color3) {
        return `linear-gradient(to bottom, ${color1} 0, ${color1} 33%, ${color2} 33%, ${color2} 67%, ${color3} 67%, ${color3} 100%)`;
    } else {
        return `linear-gradient(to bottom, ${color1} 0, ${color1} 50%, ${color2} 50%, ${color2} 100%)`;
    }
};

export default class ComponentToPrint extends React.Component {
    render() {
        const { curStudent, picture, beltSetting } = this.props;
        let beltsClass = [];
        if (beltSetting === 'a') {
            beltsClass = beltsClassMapperA;
        } else {
            beltsClass = beltsClassMapperB;
        }

        return (
            <div style={Style.renderPage}>
                <div style={Style.renderCard}>
                    <div style={Style.sectionContainer}>
                        <div style={Style.section}>
                            <QRCode
                                value={'academyhelper' + curStudent.id}
                                size={96}
                            />
                        </div>
                        <div style={Style.section}>
                            <div
                                style={Style.renderText}
                            >{`ID: ${curStudent.id}`}</div>
                            <div style={Style.renderText}>
                                {curStudent.firstname}
                            </div>
                            <div style={Style.renderText}>
                                {curStudent.lastname}
                            </div>
                        </div>
                    </div>

                    {picture === '' ? (
                        <></>
                    ) : (
                        <img src={picture} style={Style.capturedImage} />
                    )}

                    <FormControl component="fieldset" style={Style.formControl}>
                        <FormGroup style={Style.wrap}>
                            {beltsClass(curStudent.belt).map((belt) => {
                                const color = beltColorMapper(
                                    belt,
                                    beltSetting
                                );
                                return (
                                    <div style={Style.beltRow}>
                                        <FormControlLabel
                                            control={
                                                <Checkbox
                                                    checked={false}
                                                    name={belt}
                                                    style={Style.checkbox}
                                                />
                                            }
                                        />
                                        <div
                                            style={{
                                                marginTop: '0',
                                                marginBottom: '0',
                                                paddingLeft: '20px',
                                                paddingRight: '20px',
                                                width: '150px',
                                                height: '30px',
                                                background: beltBackground(
                                                    color.color[0],
                                                    color.color[1],
                                                    color.color[2]
                                                ),
                                                color: `${color.fontColor}`,
                                                border: 'solid 1px black',
                                                display: 'flex',
                                                flexDirection: 'column',
                                                justifyContent: 'center',
                                            }}
                                        >
                                            {beltMapper(belt, beltSetting)}
                                        </div>
                                    </div>
                                );
                            })}
                        </FormGroup>
                    </FormControl>
                </div>
            </div>
        );
    }
}
