import React, { useRef, useState, useEffect } from 'react';
import { Button } from '@material-ui/core';
import { useReactToPrint } from 'react-to-print';
import PadFormToPrint from './PadFormToPrint';
import { useHistory, useParams } from 'react-router-dom';
import { getStudent, getConfigInfo } from '../helper/db';

const root = {
    display: 'flex',
    flexDirection: 'column',
    '@media print': {
        display: 'block',
        pageBreakBefore: 'always',
    },
};

const divButton = {
    marginLeft: '40px',
    marginRight: 'auto',
};

const button = {
    width: 'fit-content',
    marginLeft: '5px',
    marginRight: '5px',
};

const PadForm = () => {
    const componentRef = useRef();

    const pageStyle = `
        @page {
            size: 216mm 280mm;
        }
  `;
    const handlePrint = useReactToPrint({
        content: () => componentRef.current,
        pageStyle: pageStyle,
    });
    const [curStudent, setCurStudent] = useState({});
    const [config, setConfig] = useState({
        academyname: '',
        address: '',
        phone: '',
    });
    const history = useHistory();
    const { id } = useParams();

    useEffect(() => {
        const setStudentCall = async () => {
            const result = await getStudent(id);
            if (!result) {
                history.push('/error');
                return;
            }
            setCurStudent(result);
            const resultConfig = await getConfigInfo();
            setConfig(resultConfig[0]);
        };
        setStudentCall();
    }, []);

    return (
        <div style={root}>
            <PadFormToPrint
                ref={componentRef}
                curStudent={curStudent}
                config={config}
            />
            <div style={divButton}>
                <Button
                    onClick={() => history.push(`/detailstudent/${id}`)}
                    variant="contained"
                    style={button}
                >
                    Back
                </Button>
                <Button
                    onClick={handlePrint}
                    variant="contained"
                    color="primary"
                    style={button}
                >
                    PRINT
                </Button>
            </div>
        </div>
    );
};

export default PadForm;
